export const listPost = {
  data: [
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/cQKBbAQvIhyHzoOuUnur.png",
      id: 6,
      title: "Trung tướng Trần Hồng Minh làm Bí thư Tỉnh ủy Cao Bằng",
      detail:
        '<p class="description">Chủ nhiệm Tổng cục C&ocirc;ng nghiệp quốc ph&ograve;ng - Trung tướng Trần Hồng Minh được điều động, chỉ định giữ chức B&iacute; thư Tỉnh ủy Cao Bằng nhiệm kỳ 2020 - 2025.</p>\n<article class="fck_detail ">\n<p class="Normal">Hội nghị c&ocirc;ng bố quyết định của Bộ Ch&iacute;nh trị được Ban thường vụ Tỉnh ủy Cao Bằng tổ chức s&aacute;ng 16/9.</p>\n<p class="Normal">Hai h&ocirc;m trước, &ocirc;ng Lại Xu&acirc;n M&ocirc;n nhận quyết định th&ocirc;i tham gia Ban Chấp h&agrave;nh, Ban Thường vụ v&agrave; th&ocirc;i giữ chức B&iacute; thư Tỉnh ủy Cao Bằng nhiệm kỳ 2020-2025; được điều động, ph&acirc;n c&ocirc;ng giữ chức Ph&oacute; trưởng Ban Tuy&ecirc;n gi&aacute;o Trung ương.</p>\n<p class="Normal">Trưởng Ban Tổ chức Trung ương Trương Thị Mai mong muốn Trung tướng Trần Hồng Minh tr&ecirc;n cương vị mới sẽ c&ugrave;ng đảng bộ, ch&iacute;nh quyền v&agrave; nh&acirc;n d&acirc;n tỉnh Cao Bằng ph&aacute;t huy truyền thống, ho&agrave;n th&agrave;nh tốt nhiệm vụ được giao.</p>\n<p class="Normal">T&acirc;n B&iacute; thư Tỉnh ủy Cao Bằng hứa kh&ocirc;ng ngừng phấn đấu, c&ugrave;ng tập thể l&atilde;nh đạo v&agrave; nh&acirc;n d&acirc;n trong tỉnh chung sức đưa Cao Bằng trở th&agrave;nh tỉnh năng động, s&aacute;ng tạo, ho&agrave;n th&agrave;nh c&aacute;c chỉ ti&ecirc;u Nghị quyết Đại hội Đảng bộ tỉnh lần thứ XIX đề ra.</p>\n<p class="Normal">Trung tướng Trần Hồng Minh 54 tuổi; Tiến sĩ Kỹ thuật; qu&ecirc; ở huyện Mỹ Đức, th&agrave;nh phố H&agrave; Nội. &Ocirc;ng l&agrave; Ủy vi&ecirc;n Trung ương Đảng kh&oacute;a XIII, Đại biểu Quốc hội kh&oacute;a XV.</p>\n<p class="Normal">&Ocirc;ng trải qua nhiều vị tr&iacute; c&ocirc;ng t&aacute;c ở Binh chủng C&ocirc;ng binh, từ Giảng vi&ecirc;n Trường sĩ quan C&ocirc;ng binh, đến Lữ đo&agrave;n trưởng Lữ đo&agrave;n 249, Ph&oacute; Tham mưu trưởng Binh chủng, Ph&oacute; Tư lệnh ki&ecirc;m Tham mưu trưởng,Tư lệnh Binh chủng C&ocirc;ng binh.</p>\n<p class="Normal">Từ th&aacute;ng 5/2016, &ocirc;ng giữ chức Ph&oacute; Tư lệnh Qu&acirc;n khu 1, sau đ&oacute; l&agrave; Ph&oacute; Tư lệnh ki&ecirc;m Tham mưu trưởng Qu&acirc;n khu 1, Tư lệnh Qu&acirc;n khu 1. Từ 10/2019 đến nay, &ocirc;ng đảm nhiệm cương vị Chủ nhiệm Tổng cục C&ocirc;ng nghiệp quốc ph&ograve;ng.</p>\n</article>',
      description:
        "Chủ nhiệm Tổng cục Công nghiệp quốc phòng - Trung tướng Trần Hồng Minh được điều động, chỉ định giữ chức Bí thư Tỉnh ủy Cao Bằng nhiệm kỳ 2020 - 2025.",
      createdAt: "2021-09-16T07:34:02.000Z",
      updatedAt: "2021-09-16T08:03:13.000Z",
      parent_id: 1,
      child_id: 1,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/IyeQUWoMgHlceRPjXXMR.png",
      id: 7,
      title: "Đại tướng Phùng Quang Thanh yên nghỉ tại quê nhà",
      detail:
        '<p class="description">Lễ an t&aacute;ng Đại tướng Ph&ugrave;ng Quang Thanh được tổ chức l&uacute;c 15h30 ng&agrave;y 15/9, tại qu&ecirc; nh&agrave; - x&atilde; Thạch Đ&agrave;, huyện M&ecirc; Linh, H&agrave; Nội.</p>\n<article class="fck_detail ">\n<p class="Normal">Ng&agrave;y 15/9, lễ viếng v&agrave; lễ truy điệu Đại tướng Ph&ugrave;ng Quang Thanh, nguy&ecirc;n Ủy vi&ecirc;n Bộ Ch&iacute;nh trị, nguy&ecirc;n Ph&oacute; B&iacute; thư Qu&acirc;n ủy Trung ương, nguy&ecirc;n Bộ trưởng Quốc ph&ograve;ng được tổ chức tại Nh&agrave; Tang lễ quốc gia, theo nghi thức Lễ tang cấp Nh&agrave; nước. Tổng b&iacute; thư Nguyễn Ph&uacute; Trọng gửi v&ograve;ng hoa viếng.</p>\n<p class="Normal">Chủ tịch nước Nguyễn Xu&acirc;n Ph&uacute;c, Thủ tướng Phạm Minh Ch&iacute;nh, Chủ tịch Quốc hội Vương Đ&igrave;nh Huệ..., đ&atilde; dẫn đầu c&aacute;c đo&agrave;n đại biểu Trung ương Đảng, Chủ tịch nước, Quốc hội, Ch&iacute;nh phủ v&agrave;o viếng Đại tướng Ph&ugrave;ng Quang Thanh v&agrave; chia buồn c&ugrave;ng gia quyến.</p>\n<p class="Normal">Đọc lời điếu tại lễ truy điệu, Ph&oacute; thủ tướng Phạm B&igrave;nh Minh n&oacute;i Đại tướng Ph&ugrave;ng Quang Thanh được r&egrave;n luyện, trưởng th&agrave;nh trong chiến đấu, đ&agrave;o tạo cơ bản, trải qua c&aacute;c cương vị c&ocirc;ng t&aacute;c từ chiến sĩ, qua chỉ huy c&aacute;c cấp đến Bộ trưởng Quốc ph&ograve;ng; từ binh nh&igrave; đến cấp h&agrave;m Đại tướng. Trong mọi điều kiện, ho&agrave;n cảnh, d&ugrave; ở bất cứ cương vị c&ocirc;ng t&aacute;c n&agrave;o, Đại tướng lu&ocirc;n nỗ lực phấn đấu, ho&agrave;n th&agrave;nh xuất sắc mọi nhiệm vụ được giao.</p>\n<p class="Normal">Tr&ecirc;n cương vị Ủy vi&ecirc;n Bộ Ch&iacute;nh trị, Ph&oacute; B&iacute; thư Qu&acirc;n ủy Trung ương - Bộ trưởng Bộ Quốc ph&ograve;ng, với tư duy nhạy b&eacute;n, tầm nh&igrave;n xa, Đại tướng Ph&ugrave;ng Quang Thanh đ&atilde; c&ugrave;ng tập thể Qu&acirc;n ủy Trung ương, Bộ Quốc ph&ograve;ng chủ động tham mưu chiến lược với Đảng, Nh&agrave; nước những chủ trương đ&uacute;ng, s&aacute;t trong hoạch định đường lối qu&acirc;n sự, quốc ph&ograve;ng, bảo vệ v&agrave; x&acirc;y dựng Tổ quốc; x&acirc;y dựng Qu&acirc;n đội c&aacute;ch mạng, ch&iacute;nh quy, tinh nhuệ, từng bước hiện đại, một số qu&acirc;n binh chủng, đơn vị tiến l&ecirc;n hiện đại.</p>\n<p class="Normal"><strong>Thay mặt gia đ&igrave;nh, &ocirc;ng Ph&ugrave;ng Quang Hải</strong>, con trai Đại tướng Ph&ugrave;ng Quang Thanh, gửi lời cảm ơn l&atilde;nh đạo, nguy&ecirc;n l&atilde;nh đạo Đảng, Nh&agrave; nước..., đ&atilde; thăm hỏi, chia buồn, tiễn cha m&igrave;nh về nơi an nghỉ cuối c&ugrave;ng. "Con mong bố thanh thản về c&otilde;i vĩnh hằng, nơi c&oacute; tổ ti&ecirc;n, c&aacute;c anh h&ugrave;ng liệt sĩ, những người th&acirc;n đ&atilde; khuất. Bố đ&atilde; c&oacute; một cuộc đời đ&aacute;ng sống", &ocirc;ng Ph&ugrave;ng Quang Hải n&oacute;i.</p>\n<p class="Normal">L&uacute;c 3h45 ng&agrave;y 11/9, đại tướng Ph&ugrave;ng Quang Thanh từ trần tại nh&agrave; ri&ecirc;ng, sau thời gian l&acirc;m bệnh nặng, hưởng thọ 73 tuổi.</p>\n<p class="Normal">Đại tướng Ph&ugrave;ng Quang Thanh l&agrave; Ủy vi&ecirc;n Trung ương Đảng kh&oacute;a IX, X, XI; Ủy vi&ecirc;n Bộ Ch&iacute;nh trị kh&oacute;a X, XI; đại biểu Quốc hội kh&oacute;a XI, XII, XIII.</p>\n<p class="Normal">&Ocirc;ng được Đảng, Nh&agrave; nước tặng thưởng Hu&acirc;n chương Hồ Ch&iacute; Minh, danh hiệu Anh h&ugrave;ng lực lượng vũ trang nh&acirc;n d&acirc;n, Huy hiệu 50 năm tuổi Đảng v&agrave; nhiều hu&acirc;n, huy chương cao qu&yacute; kh&aacute;c.</p>\n</article>',
      description:
        "Lễ an táng Đại tướng Phùng Quang Thanh được tổ chức lúc 15h30 ngày 15/9, tại quê nhà - xã Thạch Đà, huyện Mê Linh, Hà Nội.",
      createdAt: "2021-09-16T07:39:19.000Z",
      updatedAt: "2021-09-16T08:03:56.000Z",
      parent_id: 1,
      child_id: 1,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/QMSmgKXbrbKzlAzNqYpY.png",
      id: 8,
      title:
        "Tổng bí thư: Phải có cơ chế thay thế kịp thời cán bộ không còn uy tín",
      detail:
        '<div class="header-content width_common">\n<p class="description">C&aacute;c cơ quan Nội ch&iacute;nh tiếp tục kiện to&agrave;n tổ chức, v&agrave; phải c&oacute; cơ chế s&agrave;ng lọc, thay thế kịp thời những người kh&ocirc;ng ho&agrave;n th&agrave;nh nhiệm vụ, theo Tổng b&iacute; thư Nguyễn Ph&uacute; Trọng.</p>\n<article class="fck_detail ">\n<div>\n<p class="Normal">S&aacute;ng 15/9, Ban Nội ch&iacute;nh Trung ương, Ủy ban Kiểm tra Trung ương, Bộ Quốc ph&ograve;ng, Bộ C&ocirc;ng an, Thanh tra Ch&iacute;nh phủ, Kiểm to&aacute;n Nh&agrave; nước, Viện Kiểm s&aacute;t nh&acirc;n d&acirc;n tối cao, T&ograve;a &aacute;n nh&acirc;n d&acirc;n tối cao v&agrave; Bộ Tư ph&aacute;p tổ chức hội nghị to&agrave;n quốc triển khai nghị quyết Đại hội XIII.</p>\n<p class="Normal">Đ&acirc;y l&agrave; lần đầu ti&ecirc;n c&aacute;c cơ quan Nội ch&iacute;nh phối hợp tổ chức hội nghị to&agrave;n quốc với h&igrave;nh thức trực tuyến, từ điểm cầu Trung ương đến 138 điểm cầu tại c&aacute;c tỉnh, th&agrave;nh với hơn 4.600 đại biểu.</p>\n<p class="Normal">Ph&aacute;t biểu chỉ đạo hội nghị, Tổng b&iacute; thư Nguyễn Ph&uacute; Trọng khẳng định c&aacute;c cơ quan Nội ch&iacute;nh l&agrave; "lực lượng n&ograve;ng cốt, trung ki&ecirc;n, thanh bảo kiếm sắc b&eacute;n v&agrave; l&aacute; chắn vững chắc".</p>\n<p class="Normal">Với vai tr&ograve; v&agrave; lĩnh vực hoạt động trọng yếu, li&ecirc;n quan hầu hết, trực tiếp đến mọi mặt đời sống x&atilde; hội, Tổng b&iacute; thư y&ecirc;u cầu c&aacute;c cơ quan Nội ch&iacute;nh phải "đ&uacute;ng vai thuộc b&agrave;i"; chủ động dự b&aacute;o sớm, đ&aacute;nh gi&aacute; đ&uacute;ng t&igrave;nh h&igrave;nh; đề cao tinh thần phục vụ nh&acirc;n d&acirc;n, tạo điều kiện thuận lợi nhất để giải quyết y&ecirc;u cầu, c&ocirc;ng việc của d&acirc;n.</p>\n<p class="Normal">C&aacute;c cơ quan Nội ch&iacute;nh tiếp tục kiện to&agrave;n tổ chức bộ m&aacute;y v&agrave; x&acirc;y dựng đội ngũ c&aacute;n bộ c&oacute; đủ phẩm chất, năng lực, uy t&iacute;n; khuyến kh&iacute;ch, bảo vệ c&aacute;n bộ d&aacute;m nghĩ, d&aacute;m n&oacute;i, d&aacute;m l&agrave;m, d&aacute;m chịu tr&aacute;ch nhiệm, quyết liệt trong h&agrave;nh động v&igrave; lợi &iacute;ch chung. Đồng thời, "phải c&oacute; cơ chế s&agrave;ng lọc, thay thế kịp thời những người kh&ocirc;ng ho&agrave;n th&agrave;nh nhiệm vụ, vi phạm ph&aacute;p luật, đạo đức c&ocirc;ng vụ, đạo đức nghề nghiệp, bị xử l&yacute; kỷ luật, kh&ocirc;ng c&ograve;n uy t&iacute;n đối với nh&acirc;n d&acirc;n".</p>\n<p class="Normal">Theo Tổng b&iacute; thư, c&ocirc;ng t&aacute;c nội ch&iacute;nh thường xuy&ecirc;n đối mặt với những th&aacute;ch thức, ti&ecirc;u cực trong x&atilde; hội, những m&ocirc;i trường nhạy cảm, khiến con người rất dễ bị mua chuộc, sa ng&atilde;, "nh&uacute;ng ch&agrave;m". V&igrave; vậy, c&aacute;n bộ, chiến sĩ, c&ocirc;ng chức c&aacute;c cơ quan Nội ch&iacute;nh phải cảnh gi&aacute;c, tỉnh t&aacute;o; c&oacute; bản lĩnh, dũng kh&iacute; bảo vệ c&aacute;i đ&uacute;ng, đấu tranh ch&iacute;nh trực; đặc biệt, bản th&acirc;n phải trọng li&ecirc;m sỉ, kh&ocirc;ng chịu bất kỳ sức &eacute;p kh&ocirc;ng trong s&aacute;ng hay bất kỳ sự c&aacute;m dỗ, mua chuộc n&agrave;o...</p>\n<p class="Normal">Người đứng đầu Đảng y&ecirc;u cầu c&aacute;c cơ quan Nội ch&iacute;nh "hiệp đồng t&aacute;c chiến", v&igrave; c&ocirc;ng việc nặng nề, lại thường xuy&ecirc;n đụng chạm đến lợi &iacute;ch, đụng chạm đến con người... Tuy nhi&ecirc;n, "phối hợp chặt chẽ, nhịp nh&agrave;ng kh&ocirc;ng c&oacute; nghĩa l&agrave; dĩ ho&agrave; vi qu&yacute;, nh&acirc;n nhượng v&ocirc; nguy&ecirc;n tắc, c&ugrave;ng bỏ qua sai phạm của nhau, m&agrave; phải gắn liền với thực hiện kiểm so&aacute;t quyền lực giữa c&aacute;c cơ quan, nhằm bảo đảm tu&acirc;n thủ ph&aacute;p luật trong thực thi c&ocirc;ng vụ".</p>\n<p class="Normal">&Ocirc;ng cũng nhấn mạnh, phải tr&aacute;nh t&igrave;nh trạng "trống đ&aacute;nh xu&ocirc;i, k&egrave;n thổi ngược", "quyền anh, quyền t&ocirc;i", "cua cậy c&agrave;ng, c&aacute; cậy v&acirc;y"; mọi vấn đề vướng mắc phải được b&agrave;n bạc, giải quyết triệt để, thấu đ&aacute;o. Nếu qua trao đổi, họp b&agrave;n c&aacute;c cơ quan vẫn chưa thống nhất được, th&igrave; vận dụng cơ chế phối hợp, xin &yacute; kiến trong xử l&yacute; c&aacute;c vụ &aacute;n, vụ việc như c&aacute;ch l&agrave;m c&oacute; hiệu quả của Ban chỉ đạo Trung ương về ph&ograve;ng, chống tham nhũng.</p>\n<p class="Normal">"Hết sức lưu &yacute; kh&ocirc;ng để c&aacute;c thế lực xấu, th&ugrave; địch lợi dụng, k&iacute;ch động, chia rẽ, đưa l&ecirc;n th&agrave;nh vấn đề phức tạp, r&ecirc;u rao l&agrave; ch&uacute;ng ta đấu đ&aacute;, m&acirc;u thuẫn nội bộ", Tổng b&iacute; thư n&oacute;i.</p>\n<p class="Normal">&Ocirc;ng cũng n&ecirc;u r&otilde; gắn đấu tranh ph&ograve;ng, chống tham nhũng với ph&ograve;ng, chống ti&ecirc;u cực; ngăn chặn, đẩy l&ugrave;i suy tho&aacute;i về tư tưởng ch&iacute;nh trị, đạo đức, lối sống...</p>\n<p class="Normal">Tổng b&iacute; thư nhấn mạnh quan điểm, phải ki&ecirc;n quyết chấn chỉnh, đấu tranh loại bỏ tư tưởng b&agrave;n l&ugrave;i, l&agrave;m việc "cầm chừng", "ph&ograve;ng thủ", "che chắn", giữ "an to&agrave;n" trong một bộ phận c&aacute;n bộ, c&ocirc;ng chức, nhất l&agrave; c&aacute;n bộ l&atilde;nh đạo, quản l&yacute; cơ quan Nội ch&iacute;nh c&aacute;c cấp. "Những ai c&oacute; tư tưởng n&agrave;y th&igrave; h&atilde;y dẹp sang một b&ecirc;n cho người kh&aacute;c l&agrave;m...", &ocirc;ng n&oacute;i.</p>\n<p class="Normal">B&aacute;o c&aacute;o tại hội nghị, Trưởng ban Nội ch&iacute;nh Trung ương Phan Đ&igrave;nh Trạc cho hay trong nhiệm kỳ Đại hội XII (2016 - 2020), c&aacute;c cơ quan nội ch&iacute;nh đ&atilde; nghi&ecirc;n cứu, tham mưu Trung ương, Bộ Ch&iacute;nh trị, Ban B&iacute; thư, Quốc hội, Ch&iacute;nh phủ ban h&agrave;nh gần 200 nghị quyết, chỉ thị, kết luận, quy định của Đảng, ph&aacute;p luật của Nh&agrave; nước. Qua đ&oacute;, từng bước h&igrave;nh th&agrave;nh cơ chế ph&ograve;ng ngừa chặt chẽ để "kh&ocirc;ng thể tham nhũng"... Cũng trong giai đoạn n&agrave;y, cấp ủy, ủy ban kiểm tra c&aacute;c cấp đ&atilde; xử l&yacute; kỷ luật hơn 3.200 c&aacute;n bộ, đảng vi&ecirc;n vi phạm li&ecirc;n quan đến tham nhũng, trong đ&oacute; hơn 110 c&aacute;n bộ diện Trung ương quản l&yacute;.</p>\n<p class="Normal">Về nhiệm vụ, giải ph&aacute;p thực hiện nghị quyết Đại hội XIII, &ocirc;ng Phan Đ&igrave;nh Trạc nhấn mạnh việc c&aacute;c cơ quan Nội ch&iacute;nh ph&aacute;t huy hơn nữa vai tr&ograve; chủ c&ocirc;ng, n&ograve;ng cốt, ki&ecirc;n quyết, ki&ecirc;n tr&igrave; đấu tranh ngăn chặn, đẩy l&ugrave;i tham nhũng...</p>\n<p class="Normal">"X&acirc;y dựng nền tư ph&aacute;p Việt Nam chuy&ecirc;n nghiệp, hiện đại, c&ocirc;ng bằng, nghi&ecirc;m minh, li&ecirc;m ch&iacute;nh, phụng sự Tổ quốc, phục vụ nh&acirc;n d&acirc;n", &ocirc;ng n&oacute;i.</p>\n</div>\n</article>\n</div>',
      description:
        "Các cơ quan Nội chính tiếp tục kiện toàn tổ chức, và phải có cơ chế sàng lọc, thay thế kịp thời những người không hoàn thành nhiệm vụ, theo Tổng bí thư Nguyễn Phú Trọng.",
      createdAt: "2021-09-16T07:42:44.000Z",
      updatedAt: "2021-09-16T08:02:32.000Z",
      parent_id: 1,
      child_id: 1,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/oFZvIgyLZFudfyptxSnh.png",
      id: 9,
      title: "Đề xuất thay đổi quyền đăng ký sáng chế",
      detail:
        '<div class="header-content width_common">\n<p class="description">Dự thảo sửa đổi Luật Sở hữu tr&iacute; tuệ đề xuất tổ chức chủ tr&igrave; nghi&ecirc;n cứu c&oacute; quyền đăng k&yacute; s&aacute;ng chế v&agrave; c&oacute; quyền sở hữu s&aacute;ng chế khi được cấp văn bằng bảo hộ.</p>\n<article class="fck_detail ">\n<p class="Normal">Chiều 14/9, Thường vụ Quốc hội cho &yacute; kiến về dự &aacute;n Luật sửa đổi, bổ sung một số điều của Luật Sở hữu tr&iacute; tuệ. Đại diện cơ quan soạn thảo, Bộ trưởng Khoa học v&agrave; C&ocirc;ng nghệ Huỳnh Th&agrave;nh Đạt khẳng định, sở hữu tr&iacute; tuệ l&agrave; yếu tố quan trọng để ph&aacute;t triển kinh tế, x&atilde; hội v&igrave; t&agrave;i sản tr&iacute; tuệ c&oacute; gi&aacute; trị lớn trong nền kinh tế tri thức, l&agrave; nguồn lực cho ph&aacute;t triển sản xuất kinh doanh v&agrave; l&agrave; c&ocirc;ng cụ cạnh tranh hiệu quả của doanh nghiệp v&agrave; nền kinh tế.</p>\n<p class="Normal">Qua 16 năm &aacute;p dụng, Luật Sở hữu tr&iacute; tuệ đ&atilde; ph&aacute;t huy vai tr&ograve; trong việc điều chỉnh c&aacute;c quan hệ ph&aacute;p luật với loại t&agrave;i sản đặc biệt n&agrave;y. Tuy nhi&ecirc;n, theo &ocirc;ng Đạt, bối cảnh ph&aacute;t triển hiện nay c&oacute; nhiều thay đổi so với năm 2005. Việt Nam kh&ocirc;ng c&ograve;n thuần t&uacute;y l&agrave; nước "sử dụng t&agrave;i sản tr&iacute; tuệ" m&agrave; đang chuyển mạnh sang "tạo ra t&agrave;i sản n&agrave;y phục vụ cho m&ocirc; h&igrave;nh tăng trưởng theo chiều s&acirc;u". Do đ&oacute;, việc sửa đổi, bổ sung Luật Sở hữu tr&iacute; tuệ lần n&agrave;y l&agrave; y&ecirc;u cầu cấp thiết.</p>\n<p class="Normal">Một trong những điểm mới của dự &aacute;n luật l&agrave; đề xuất c&aacute;c tổ chức chủ tr&igrave; nghi&ecirc;n cứu c&oacute; quyền đăng k&yacute; s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; (l&agrave; kết quả của nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước) v&agrave; c&oacute; quyền sở hữu khi được cấp văn bằng bảo hộ, trừ tổ chức thuộc lĩnh vực quốc ph&ograve;ng, an ninh vẫn do c&aacute;c cơ quan nh&agrave; nước thực hiện đăng k&yacute;.</p>\n<p class="Normal">Theo người đứng đầu ng&agrave;nh Khoa học v&agrave; C&ocirc;ng nghệ, phương &aacute;n n&agrave;y được x&acirc;y dựng nhằm thể chế h&oacute;a Nghị quyết số 20 ng&agrave;y 1/11/2012 của Hội nghị Trung ương 6 Kh&oacute;a XI về ph&aacute;t triển Khoa học C&ocirc;ng nghệ phục vụ sự nghiệp c&ocirc;ng nghiệp h&oacute;a, hiện đại h&oacute;a trong điều kiện kinh tế thị trường định hướng x&atilde; hội chủ nghĩa v&agrave; hội nhập quốc tế. Nghị quyết c&oacute; chủ trương: "Giao quyền sở hữu c&aacute;c kết quả nghi&ecirc;n cứu khoa học v&agrave; ph&aacute;t triển c&ocirc;ng nghệ c&oacute; sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước cho cơ quan chủ tr&igrave; nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ".</p>\n<p class="Normal">"Quy định n&agrave;y sẽ tạo động lực khuyến kh&iacute;ch c&aacute;c tổ chức chủ tr&igrave; đăng k&yacute; x&aacute;c lập quyền sở hữu c&ocirc;ng nghiệp với phần kết quả nghi&ecirc;n cứu c&oacute; khả năng bảo hộ v&agrave; th&uacute;c đẩy thương mại h&oacute;a đối tượng quyền sở hữu c&ocirc;ng nghệ đ&oacute;", Bộ trưởng Khoa học C&ocirc;ng nghệ n&oacute;i.</p>\n<p class="Normal">Theo &ocirc;ng, phương &aacute;n n&agrave;y được sự đồng t&igrave;nh của đa số th&agrave;nh vi&ecirc;n Ch&iacute;nh phủ v&igrave; vẫn bảo đảm được sự quản l&yacute; của Nh&agrave; nước đối với kết quả nghi&ecirc;n cứu khoa học c&ocirc;ng nghệ được bảo hộ sở hữu c&ocirc;ng nghệ c&oacute; sử dụng ng&acirc;n s&aacute;ch. Ph&aacute;p luật của nhiều quốc gia tr&ecirc;n thế giới cũng c&oacute; quy định tương tự để th&uacute;c đẩy hoạt động nghi&ecirc;n cứu khoa học c&ocirc;ng nghệ, thương mại h&oacute;a kết quả nghi&ecirc;n cứu phục vụ ph&aacute;t triển kinh tế, x&atilde; hội. Tuy nhi&ecirc;n, để thực hiện phương &aacute;n, một số văn bản ph&aacute;p luật c&oacute; li&ecirc;n quan cần sửa đổi, bổ sung nhằm bảo đảm sự thống nhất trong hệ thống ph&aacute;p luật.</p>\n<p class="Normal">Do số &iacute;t th&agrave;nh vi&ecirc;n Ch&iacute;nh phủ đề nghị giữ nguy&ecirc;n quy định ph&aacute;p luật hiện h&agrave;nh, Ban soạn thảo đ&atilde; thiết kế phương &aacute;n 2, tr&igrave;nh xin &yacute; kiến Quốc hội. Theo đ&oacute;, quyền đăng k&yacute; s&aacute;ng chế vẫn thuộc về tổ chức, cơ quan nh&agrave; nước được giao quyền chủ đầu tư. Việc n&agrave;y sẽ bảo đảm được nguy&ecirc;n tắc t&agrave;i sản được tạo ra do Nh&agrave; nước đầu tư thuộc về Nh&agrave; nước.</p>\n<p class="Normal">D&ugrave; vậy, trong trường hợp tổ chức, cơ quan nh&agrave; nước được giao quyền chủ đầu tư thực hiện quyền đăng k&yacute; v&agrave; trở th&agrave;nh chủ văn bằng bảo hộ th&igrave; tổ chức, cơ quan đ&oacute; cũng kh&ocirc;ng c&oacute; khả năng thương mại h&oacute;a đối tượng được bảo hộ do cơ quan quản l&yacute; nh&agrave; nước kh&ocirc;ng c&oacute; chức năng kinh doanh. Việc giữ nguy&ecirc;n quy định hiện h&agrave;nh cũng kh&ocirc;ng thể giải quyết được c&aacute;c bất cập hiện nay do kh&ocirc;ng đủ căn cứ ph&aacute;p l&yacute; cho tổ chức chủ tr&igrave; kịp thời thực hiện đăng k&yacute; s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; l&agrave; kết quả nhiệm vụ khoa học c&ocirc;ng nghệ sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước trong một số trường hợp.</p>\n<p class="Normal">"Giữ quy định như cũ kh&ocirc;ng th&uacute;c đẩy được việc khai th&aacute;c, thương mại h&oacute;a; l&agrave;m cho gi&aacute; trị đầu tư của nh&agrave; nước trở n&ecirc;n k&eacute;m hiệu quả v&agrave; quan trọng nhất l&agrave; kh&ocirc;ng thể \'cởi tr&oacute;i\' để th&uacute;c đẩy nguồn lực đổi mới s&aacute;ng tạo của cả nền kinh tế", l&atilde;nh đạo Bộ Khoa học v&agrave; C&ocirc;ng nghệ nhấn mạnh.</p>\n<p class="Normal">Đại diện cơ quan thẩm tra, Chủ nhiệm Ủy ban Ph&aacute;p luật Ho&agrave;ng Thanh T&ugrave;ng cho biết, thường trực Ủy ban cơ bản nhất tr&iacute; với phương &aacute;n 1 của Ch&iacute;nh phủ. Tuy nhi&ecirc;n, &ocirc;ng T&ugrave;ng đề nghị Ban soạn thảo cần tiếp tục r&agrave; so&aacute;t, ho&agrave;n thiện phương &aacute;n n&agrave;y theo hướng bổ sung, l&agrave;m r&otilde;&nbsp;<em>"cơ chế ph&acirc;n chia hợp l&yacute; lợi &iacute;ch giữa Nh&agrave; nước, cơ quan chủ tr&igrave; v&agrave; t&aacute;c giả"</em>&nbsp;để thể chế h&oacute;a đầy đủ chủ trương của Đảng tại Nghị quyết số 20-NQ/TW.</p>\n<p class="Normal">Đồng thời, Ch&iacute;nh phủ cũng cần nghi&ecirc;n cứu mở rộng cơ chế giao quyền sở hữu tương tự đối với giống c&acirc;y trồng, quyền t&aacute;c giả v&agrave; quyền li&ecirc;n quan; khắc phục sự kh&ocirc;ng thống nhất giữa quy định về quyền đăng k&yacute;, sở hữu với kết quả của nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ c&oacute; sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước trong c&aacute;c luật hiện h&agrave;nh để bảo đảm t&iacute;nh thống nhất của hệ thống ph&aacute;p luật.</p>\n<p class="Normal">Chủ nhiệm Ủy ban Khoa học C&ocirc;ng nghệ M&ocirc;i trường L&ecirc; Quang Huy đồng t&igrave;nh với đề xuất của Ch&iacute;nh phủ ở phương &aacute;n 1, n&ecirc;n giao quyền cho c&aacute;c tổ chức khoa học c&ocirc;ng nghệ. Theo &ocirc;ng, như vậy sẽ khuyến kh&iacute;ch được sự s&aacute;ng tạo của c&aacute;c tổ chức nghi&ecirc;n cứu "nhưng cần t&iacute;nh to&aacute;n kỹ, đảm bảo ph&ugrave; hợp với luật Khoa học v&agrave; C&ocirc;ng nghệ, Đa dạng sinh học Việt Nam v&agrave; ph&aacute;p luật li&ecirc;n quan".</p>\n<div>\n<p class="Normal">Chủ tịch Quốc hội Vương Đ&igrave;nh Huệ nhấn mạnh, đ&acirc;y l&agrave; luật chuy&ecirc;n ng&agrave;nh, rất kh&oacute; nhưng sự chuẩn bị của ban soạn thảo v&agrave; thẩm tra sơ bộ của Ủy ban Ph&aacute;p luật "rất c&ocirc;ng phu, kỹ lưỡng". Dự &aacute;n luật đ&atilde; cơ bản thể chế h&oacute;a được chủ trương của Đảng, Nh&agrave; nước; ph&ugrave; hợp với Nghị quyết Trung ương 5, Kh&oacute;a XII về ho&agrave;n thiện thể chế kinh tế thị trường định hướng x&atilde; hội chủ nghĩa...</p>\n<p class="Normal">"T&ocirc;i đồng t&igrave;nh với phương &aacute;n 1 của Ch&iacute;nh phủ, giao quyền đăng k&yacute; s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; l&agrave; kết quả của nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước cho c&aacute;c tổ chức chủ tr&igrave; nghi&ecirc;n cứu; nhưng cần phải h&agrave;i h&ograve;a lợi &iacute;ch giữa đơn vị chủ quản, lợi &iacute;ch nh&agrave; nước v&agrave; lợi &iacute;ch t&aacute;c giả", &ocirc;ng Huệ n&oacute;i.</p>\n<p class="Normal">Dự &aacute;n Luật sửa đổi, bổ sung một số điều của Luật Sở hữu tr&iacute; tuệ sẽ được tr&igrave;nh xin &yacute; kiến đại biểu tại kỳ họp thứ 2, Quốc hội kh&oacute;a XV khai mạc cuối th&aacute;ng 10.</p>\n<p class="Normal">Dự thảo Luật sửa đổi, bổ sung 93 điều (trong đ&oacute; bổ sung 12 điều, sửa đổi 81 điều) v&agrave; b&atilde;i bỏ một điều, n&acirc;ng tổng số điều của Luật Sở hữu tr&iacute; tuệ sau khi sửa đổi l&ecirc;n 233 điều, thuộc 7 nh&oacute;m Ch&iacute;nh s&aacute;ch đ&atilde; được th&ocirc;ng qua. Đ&oacute; l&agrave; ch&iacute;nh s&aacute;ch bảo đảm quy định r&otilde; về t&aacute;c giả, chủ sở hữu quyền t&aacute;c giả, người biểu diễn, chủ sở hữu quyền li&ecirc;n quan trong c&aacute;c trường hợp chuyển nhượng, chuyển giao quyền t&aacute;c giả, quyền li&ecirc;n quan; khuyến kh&iacute;ch tạo ra, khai th&aacute;c v&agrave; phổ biến s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; được tạo ra từ ng&acirc;n s&aacute;ch Nh&agrave; nước; tạo thuận lợi cho qu&aacute; tr&igrave;nh thực hiện thủ tục đăng k&yacute; quyền t&aacute;c giả, quyền li&ecirc;n quan, thủ tục x&aacute;c lập quyền sở hữu c&ocirc;ng nghiệp; bảo đảm mức độ thỏa đ&aacute;ng v&agrave; c&acirc;n bằng trong bảo hộ quyền sở hữu tr&iacute; tuệ; tăng cường hiệu quả hoạt động hỗ trợ về sở hữu tr&iacute; tuệ; n&acirc;ng cao hiệu quả của hoạt động bảo vệ quyền sở hữu tr&iacute; tuệ; bảo đảm thi h&agrave;nh đầy đủ v&agrave; nghi&ecirc;m t&uacute;c c&aacute;c cam kết quốc tế của Việt Nam về bảo hộ sở hữu tr&iacute; tuệ trong qu&aacute; tr&igrave;nh hội nhập.</p>\n</div>\n</article>\n</div>',
      description:
        "Dự thảo sửa đổi Luật Sở hữu trí tuệ đề xuất tổ chức chủ trì nghiên cứu có quyền đăng ký sáng chế và có quyền sở hữu sáng chế khi được cấp văn bằng bảo hộ.",
      createdAt: "2021-09-16T07:45:29.000Z",
      updatedAt: "2021-09-16T08:04:59.000Z",
      parent_id: 1,
      child_id: 1,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/NnsACzSrkOWfqVWaUHMI.png",
      id: 10,
      title:
        "Ba chức danh lãnh đạo chủ chốt được giới thiệu với số phiếu 'rất tập trung'",
      detail:
        '<div class="header-content width_common">\n<p class="description">Hội nghị Trung ương bỏ phiếu giới thiệu nh&acirc;n sự ứng cử c&aacute;c chức danh Chủ tịch nước, Thủ tướng, Chủ tịch Quốc hội với số phiếu tập trung cao.</p>\n<article class="fck_detail ">\n<p class="Normal">Sau hơn một ng&agrave;y l&agrave;m việc, hội nghị Trung ương 2 (kh&oacute;a XIII) bế mạc s&aacute;ng 9/3. Về việc giới thiệu nh&acirc;n sự l&atilde;nh đạo cấp cao của c&aacute;c cơ quan nh&agrave; nước, Tổng b&iacute; thư, Chủ tịch nước Nguyễn Ph&uacute; Trọng cho biết, "Trung ương đ&atilde; d&acirc;n chủ thảo luận, xem x&eacute;t kỹ lưỡng, lựa chọn phương &aacute;n hợp l&yacute; nhất trong điều kiện c&oacute; thể v&agrave; đ&atilde; đạt được sự nhất tr&iacute; rất cao".</p>\n<p class="Normal">Ban chấp h&agrave;nh Trung ương khẳng định, cần sớm kiện to&agrave;n, sắp xếp lại c&aacute;c chức danh l&atilde;nh đạo cơ quan nh&agrave; nước; nhất tr&iacute; cho thực hiện việc n&agrave;y tại kỳ họp thứ 11, Quốc hội kho&aacute; XIV (dự kiến khai mạc 24/3).</p>\n<p class="Normal">Tại hội nghị, Trung ương đ&atilde; bỏ phiếu giới thiệu nh&acirc;n sự ứng cử c&aacute;c chức danh Chủ tịch nước, Thủ tướng, Chủ tịch Quốc hội - l&agrave; những chức danh l&atilde;nh đạo chủ chốt của Nh&agrave; nước, "với số phiếu rất tập trung, thể hiện sự đo&agrave;n kết thống nhất cao trong Ban chấp h&agrave;nh Trung ương".</p>\n<p class="Normal">Bộ Ch&iacute;nh trị cũng đ&atilde; b&aacute;o c&aacute;o xin &yacute; kiến Trung ương trước khi ch&iacute;nh thức giới thiệu nh&acirc;n sự đối với c&aacute;c chức danh kh&aacute;c để Quốc hội xem x&eacute;t, bầu hoặc ph&ecirc; chuẩn theo quy định của Hiến ph&aacute;p v&agrave; ph&aacute;p luật.</p>\n<p class="Normal">Tổng b&iacute; thư, Chủ tịch nước Nguyễn Ph&uacute; Trọng n&oacute;i Bộ Ch&iacute;nh trị sẽ chỉ đạo c&aacute;c cơ quan chức năng tiếp thu &yacute; kiến của Trung ương, ho&agrave;n chỉnh phương &aacute;n giới thiệu nh&acirc;n sự ứng cử chức danh l&atilde;nh đạo c&aacute;c cơ quan nh&agrave; nước, để chuẩn bị tr&igrave;nh Quốc hội. "Đ&acirc;y l&agrave; một bước quan trọng trong việc bố tr&iacute;, sắp xếp, kiện to&agrave;n đội ngũ c&aacute;n bộ của hệ thống ch&iacute;nh trị trong nhiệm kỳ n&agrave;y v&agrave; cho ch&uacute;ng ta th&ecirc;m kinh nghiệm để tiến h&agrave;nh c&aacute;c bước tiếp theo", &ocirc;ng n&ecirc;u r&otilde;.</p>\n<p class="Normal">Sau hội nghị Trung ương 2 (kh&oacute;a XIII), c&ugrave;ng với việc l&atilde;nh đạo thực hiện tốt c&ocirc;ng t&aacute;c nh&acirc;n sự cấp cao tại kỳ họp thứ 11, Quốc hội kho&aacute; XIV, c&aacute;c cấp c&oacute; thẩm quyền sẽ tiếp tục kiện to&agrave;n l&atilde;nh đạo c&aacute;c cơ quan của Trung ương Đảng, c&aacute;c ban c&aacute;n sự đảng v&agrave; đảng đo&agrave;n ở những nơi c&oacute; sự thay đổi nh&acirc;n sự, "bảo đảm sự th&ocirc;ng suốt, li&ecirc;n tục trong c&aacute;c hoạt động của to&agrave;n hệ thống ch&iacute;nh trị".</p>\n<p class="Normal"><strong>Đề cập tới chương tr&igrave;nh l&agrave;m việc to&agrave;n kh&oacute;a của Ban chấp h&agrave;nh Trung ương kh&oacute;a XIII</strong>, Tổng b&iacute; thư, Chủ tịch nước Nguyễn Ph&uacute; Trọng n&oacute;i Trung ương cơ bản t&aacute;n th&agrave;nh với tờ tr&igrave;nh v&agrave; dự thảo chương tr&igrave;nh l&agrave;m việc; đồng thời, nhấn mạnh việc x&aacute;c định nội dung chương tr&igrave;nh cần b&aacute;m s&aacute;t nghị quyết v&agrave; c&aacute;c văn kiện của Đại hội XIII.</p>\n<p class="Normal">Chương tr&igrave;nh l&agrave;m việc to&agrave;n kh&oacute;a của Trung ương cần tập trung cụ thể h&oacute;a v&agrave; chỉ đạo thực hiện 6 nhiệm vụ trọng t&acirc;m, những vấn đề thực tiễn bức thiết đặt ra, những kh&oacute; khăn cần th&aacute;o gỡ, giải quyết, những kh&acirc;u đột ph&aacute;, c&aacute;c nhiệm vụ trọng yếu, như: Tiếp tục đẩy mạnh thực hiện nghị quyết Trung ương 4 kho&aacute; XI, kh&oacute;a XII về x&acirc;y dựng Đảng; đẩy mạnh đấu tranh ph&ograve;ng, chống tham nhũng, l&atilde;ng ph&iacute;; đổi mới m&ocirc; h&igrave;nh tăng trưởng, cơ cấu lại nền kinh tế, n&acirc;ng cao chất lượng tăng trưởng, năng suất lao động, sức cạnh tranh của nền kinh tế; chủ động v&agrave; t&iacute;ch cực hội nhập quốc tế...</p>\n<p class="Normal">Để thực hiện tốt chương tr&igrave;nh l&agrave;m việc to&agrave;n kh&oacute;a đ&atilde; được Trung ương th&ocirc;ng qua, ngay sau hội nghị n&agrave;y, Tổng b&iacute; thư, Chủ tịch nước Nguyễn Ph&uacute; Trọng cho biết, Bộ Ch&iacute;nh trị sẽ chỉ đạo khẩn trương nghi&ecirc;n cứu, x&acirc;y dựng kế hoạch triển khai một c&aacute;ch nghi&ecirc;m t&uacute;c; ph&acirc;n c&ocirc;ng r&otilde; r&agrave;ng c&aacute;c tổ chức, c&aacute; nh&acirc;n chịu tr&aacute;ch nhiệm chuẩn bị c&aacute;c đề &aacute;n cụ thể.</p>\n<p class="Normal">Hội nghị Trung ương 2 (kh&oacute;a XIII) khai mạc s&aacute;ng 8/3, xem x&eacute;t v&agrave; b&agrave;n về chương tr&igrave;nh l&agrave;m việc to&agrave;n kh&oacute;a; việc giới thiệu nh&acirc;n sự l&atilde;nh đạo cấp cao của c&aacute;c cơ quan Nh&agrave; nước v&agrave; một số vấn đề quan trọng kh&aacute;c.</p>\n</article>\n</div>',
      description:
        "Hội nghị Trung ương bỏ phiếu giới thiệu nhân sự ứng cử các chức danh Chủ tịch nước, Thủ tướng, Chủ tịch Quốc hội với số phiếu tập trung cao.",
      createdAt: "2021-09-16T08:09:12.000Z",
      updatedAt: "2021-09-16T08:09:12.000Z",
      parent_id: 1,
      child_id: 2,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/SttIRtHOvCPfkZjnaXVW.png",
      id: 11,
      title: "Cần Thơ đề xuất bắc thêm cầu qua sông Hậu",
      detail:
        '<div class="header-content width_common">\n<p class="description">Cầu &Ocirc; M&ocirc;n bắc qua s&ocirc;ng Hậu nối Cần Thơ với Đồng Th&aacute;p, dự kiến tổng mức đầu tư 7.000 tỷ đồng, trong đ&oacute; đề nghị Trung ương hỗ trợ 35%.</p>\n<article class="fck_detail ">\n<p class="Normal">Ng&agrave;y 15/9, Chủ tịch UBND TP Cần Thơ Trần Việt Trường k&yacute; văn bản tr&igrave;nh Bộ Kế hoạch v&agrave; Đầu tư xem x&eacute;t, chấp thuận tổng hợp, hỗ trợ từ ng&acirc;n s&aacute;ch Trung ương giai đoạn 2021-2025 để thực hiện dự &aacute;n cầu nối quận &Ocirc; M&ocirc;n (TP Cần Thơ) với huyện Lai Vung (tỉnh Đồng Th&aacute;p).</p>\n<p class="Normal">C&ocirc;ng tr&igrave;nh sẽ nằm giữa cầu V&agrave;m Cống v&agrave; cầu Cần Thơ, c&oacute; mục ti&ecirc;u kết nối giao th&ocirc;ng li&ecirc;n v&ugrave;ng từ Ki&ecirc;n Giang- Cần Thơ- Đồng Th&aacute;p. Cụ thể l&agrave; kết nối từ tuyến đường &Ocirc; M&ocirc;n (Cần Thơ) với huyện Giồng Riềng (Ki&ecirc;n Giang), đấu nối với đường tỉnh 853 tr&ecirc;n địa b&agrave;n tỉnh Đồng Th&aacute;p v&agrave; định hướng kết nối v&agrave;o cầu Mỹ Thuận 2 đang được đầu tư x&acirc;y dựng.</p>\n<p class="Normal">Dự kiến cầu được đầu tư theo h&igrave;nh thức đối t&aacute;c c&ocirc;ng tư (PPP) trong giai đoạn 2023-2028, c&oacute; tổng mức đầu tư 7.000 tỷ đồng. Trong đ&oacute;, Cần Thơ đề xuất ng&acirc;n s&aacute;ch Trung ương hỗ trợ 2.500 tỷ đồng; ng&acirc;n s&aacute;ch địa phương v&agrave; huy động c&aacute;c nguồn vốn hợp ph&aacute;p kh&aacute;c 4.500 tỷ đồng.</p>\n<p class="Normal">UBND TP Cần Thơ cho biết, sau khi Trung ương chấp thuận, địa phương sẽ phối hợp tỉnh Đồng Th&aacute;p v&agrave; c&aacute;c bộ, ng&agrave;nh li&ecirc;n quan triển khai c&aacute;c thủ tục đầu tư.</p>\n</article>\n</div>',
      description:
        "Cầu Ô Môn bắc qua sông Hậu nối Cần Thơ với Đồng Tháp, dự kiến tổng mức đầu tư 7.000 tỷ đồng, trong đó đề nghị Trung ương hỗ trợ 35%.",
      createdAt: "2021-09-16T08:12:07.000Z",
      updatedAt: "2021-09-16T08:12:07.000Z",
      parent_id: 1,
      child_id: 3,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/NtAkPlaWtzttwmJdkfNH.png",
      id: 12,
      title: "Khủng hoảng học online",
      detail:
        '<div class="header-content width_common">\n<p class="description"><span class="location-stamp">PHILIPPINES - </span>Học online l&agrave; lựa chọn duy nhất của hai con nh&agrave; Talic, ngoại &ocirc; Manila suốt hai năm qua, giữa mu&ocirc;n nỗi lo về chi ph&iacute; v&agrave; mạng Internet.</p>\n<article class="fck_detail ">\n<p class="Normal">H&agrave;ng triệu trẻ em Philippines đang phải ở nh&agrave; năm thứ hai li&ecirc;n tiếp v&igrave; đại dịch, l&agrave;m dấy l&ecirc;n lo ngại về một cuộc khủng hoảng gi&aacute;o dục ng&agrave;y c&agrave;ng tồi tệ hơn, khi khả năng tiếp cận Internet tại quốc gia n&agrave;y hiện kh&ocirc;ng đồng đều.</p>\n<p class="Normal">L&yacute; giải việc đ&oacute;ng cửa trường tiểu học v&agrave; trung học, Tổng thống Rodrigo Duterte nhấn mạnh học sinh v&agrave; gia đ&igrave;nh c&aacute;c em cần được bảo vệ trước virus. Philippines l&agrave; một trong những nước c&oacute; tỷ lệ ti&ecirc;m chủng thấp nhất ch&acirc;u &Aacute; với 16% d&acirc;n số được ti&ecirc;m vaccine đầy đủ v&agrave; số ca nhiễm biến chủng Delta đang c&oacute; chiều hướng tăng những th&aacute;ng gần đ&acirc;y.</p>\n<p class="Normal">Điều n&agrave;y biến Philippines, với khoảng 27 triệu học sinh, trở th&agrave;nh một trong số rất &iacute;t c&aacute;c quốc gia đ&atilde; đ&oacute;ng cửa ho&agrave;n to&agrave;n trường học trong đại dịch, b&ecirc;n cạnh Venezuela, theo Quỹ Nhi đồng Li&ecirc;n Hợp Quốc (UNICEF). C&aacute;c nước kh&aacute;c từng đ&oacute;ng cửa trường học như Bangladesh, Arab Saudi hay Kuwait đ&atilde; chuyển sang giai đoạn mở cửa trở lại.</p>\n<p class="Normal">"T&ocirc;i kh&ocirc;ng thể đ&aacute;nh cược sức khỏe của trẻ em", Tổng thống Duterte hồi th&aacute;ng 6 n&oacute;i, b&aacute;c bỏ khuyến nghị từ Bộ Y tế về mở cửa trở lại trường học.</p>\n<p class="Normal">Việc đ&oacute;ng cửa gần 2.000 trường học đ&atilde; g&acirc;y nhiều kh&oacute; khăn cho c&aacute;c phụ huynh v&agrave; học sinh Philippines. Rất nhiều người, đặc biệt l&agrave; d&acirc;n ở v&ugrave;ng hẻo l&aacute;nh hay n&ocirc;ng th&ocirc;n, kh&ocirc;ng c&oacute; m&aacute;y t&iacute;nh v&agrave; Internet ở nh&agrave; để học trực tuyến.</p>\n<p class="Normal">Iljon Roxas, 16 tuổi, một học sinh trung học tại th&agrave;nh phố Bacoor, ph&iacute;a nam thủ đ&ocirc; Manila, cho biết việc nh&igrave;n chằm chằm v&agrave;o m&aacute;y t&iacute;nh suốt một năm qua khiến em kh&oacute; tập trung. Iljon chỉ mong trở lại lớp học thực sự.</p>\n<p class="Normal">"Em nhớ rất nhiều thứ, v&iacute; như việc được tr&ograve; chuyện với c&aacute;c bạn c&ugrave;ng lớp v&agrave;o giờ nghỉ", Iljon n&oacute;i. "Tin hay kh&ocirc;ng th&igrave; t&ugrave;y nhưng em c&ograve;n nhớ cả thầy c&ocirc;. Từ năm ngo&aacute;i đến nay, ch&uacute;ng em bị mắc kẹt trước m&agrave;n h&igrave;nh m&aacute;y t&iacute;nh".</p>\n<p class="Normal">Philippines kh&ocirc;ng phải quốc gia duy nhất rơi v&agrave;o t&igrave;nh cảnh n&agrave;y. Nhiều nước cũng đang chật vật t&igrave;m c&aacute;ch c&acirc;n bằng giữa nhiệm vụ đảm bảo an to&agrave;n sức khỏe cho cộng đồng v&agrave; gi&aacute;o dục trẻ em.</p>\n<p class="Normal">Một số nước, như Anh, vẫn để trường học mở cửa ngay cả khi số ca nhiễm tăng mạnh v&igrave; biến chủng Delta. Trong khi đ&oacute;, tại những nơi trường học đ&oacute;ng cửa suốt qu&atilde;ng thời gian d&agrave;i như Philippines, c&aacute;c chuy&ecirc;n gia gi&aacute;o dục lo ngại sẽ tạo ra một thế hệ học sinh lạc l&otilde;ng, bị hạn chế khả năng tiếp thu hay b&agrave;y tỏ &yacute; kiến bởi việc học tập từ xa v&agrave; c&aacute;c bậc phụ huynh qu&aacute; vất vả v&igrave; phải l&agrave;m thay phần việc chuy&ecirc;n m&ocirc;n của gi&aacute;o vi&ecirc;n.</p>\n<p class="Normal">Maritess Talic, 46 tuổi, cho biết c&ocirc; sợ rằng hai con m&igrave;nh c&oacute; thể kh&ocirc;ng học được g&igrave; trong suốt một năm qua. C&ocirc; v&agrave; chồng chỉ l&agrave; những lao động ch&acirc;n tay b&igrave;nh thường với thu nhập bấp b&ecirc;nh. Họ đ&atilde; phải t&iacute;ch g&oacute;p 5.000 peso (khoảng 100 USD) để mua một chiếc m&aacute;y t&iacute;nh bảng cũ cho hai con m&igrave;nh, một đứa 7 tuổi, một đứa 9 tuổi, học online.</p>\n<p class="Normal">Nhưng gia đ&igrave;nh Talic, sống tại Imus, khu ngoại &ocirc; ph&iacute;a nam thủ đ&ocirc; Manila, kh&ocirc;ng c&oacute; đường truyền Internet ổn định. Họ thanh to&aacute;n tiền mạng bằng thẻ trả trước n&ecirc;n dung lượng dữ liệu đ&ocirc;i khi hết ngay giữa chừng lớp học trực tuyến của hai đứa trẻ. Talic c&ograve;n gặp kh&ocirc;ng &iacute;t kh&oacute; khăn khi dạy c&aacute;c con m&ocirc;n to&aacute;n v&agrave; khoa học bởi tr&igrave;nh độ học vấn của c&ocirc; cũng kh&ocirc;ng cao.</p>\n<p class="Normal">"Mọi việc thực sự kh&ocirc;ng dễ d&agrave;ng", Talic n&oacute;i, th&ecirc;m rằng hai đứa trẻ c&ograve;n gặp kh&oacute; khăn khi phải d&ugrave;ng chung chiếc m&aacute;y t&iacute;nh bảng. "Ch&uacute;ng t&ocirc;i đ&ocirc;i l&uacute;c c&ograve;n kh&ocirc;ng c&oacute; đủ tiền trả h&oacute;a đơn điện, vậy m&agrave; giờ đ&acirc;y, ch&uacute;ng t&ocirc;i c&ograve;n phải chi th&ecirc;m tiền để mua thẻ Internet trả trước".</p>\n<p class="Normal">Talic hiểu phải ưu ti&ecirc;n sức khỏe cộng đồng hơn l&agrave; mở cửa trường học, nhưng c&ocirc; rất lo lắng cho tương lai c&aacute;c con. "Vấn đề l&agrave; t&ocirc;i kh&ocirc;ng nghĩ ch&uacute;ng tiếp thu được g&igrave;", c&ocirc; n&oacute;i. "Kết nối Internet nhiều l&uacute;c qu&aacute; chậm".</p>\n<p class="Normal">Ngay cả trước khi đại dịch b&ugrave;ng ph&aacute;t, Philippines đ&atilde; đối mặt với khủng hoảng gi&aacute;o dục, với lớp học qu&aacute; đ&ocirc;ng, cơ sở hạ tầng của c&aacute;c trường c&ocirc;ng k&eacute;m chất lượng v&agrave; mức lương thấp g&acirc;y ra t&igrave;nh trạng thiếu gi&aacute;o vi&ecirc;n nghi&ecirc;m trọng.</p>\n<p class="Normal">Một b&aacute;o c&aacute;o của Ng&acirc;n h&agrave;ng Thế giới (WB) năm 2020 cho biết độ phủ Internet của Philippines cũng kh&ocirc;ng đồng đều. Năm 2018, khoảng 57% trong 23 triệu hộ gia đ&igrave;nh nước n&agrave;y c&oacute; mạng Internet. Ch&iacute;nh phủ đang nỗ lực để cải thiện t&igrave;nh trạng mất c&acirc;n bằng về khả năng tiếp cận kỹ thuật số trong cộng đồng.</p>\n<p class="Normal">Thị trưởng Manila Francisco Domagoso năm ngo&aacute;i cho biết T&ograve;a Thị ch&iacute;nh đ&atilde; ph&aacute;t 130.000 m&aacute;y t&iacute;nh bảng c&ugrave;ng 11.000 m&aacute;y t&iacute;nh x&aacute;ch tay hỗ trợ học sinh v&agrave; gi&aacute;o vi&ecirc;n.</p>\n<p class="Normal">UNICEF hồi th&aacute;ng 8 lưu &yacute; việc đ&oacute;ng cửa trường học g&acirc;y ảnh hưởng đặc biệt nghi&ecirc;m trọng tới trẻ em dễ bị tổn thương vốn phải đối diện c&aacute;c th&aacute;ch thức về đ&oacute;i ngh&egrave;o v&agrave; bất b&igrave;nh đẳng. Họ k&ecirc;u gọi mở cửa trường học tại Philippines, bắt đầu từ những khu vực nguy cơ thấp v&agrave; &aacute;p dụng c&aacute;c quy tr&igrave;nh an to&agrave;n nghi&ecirc;m ngặt.</p>\n<p class="Normal">Theo đại diện UNICEF tại Philippines Oyunsaikhan Dendevnorov, việc đ&oacute;ng cửa trường học mang đến h&agrave;ng loạt hệ lụy cho học sinh, như nguy cơ gặp c&aacute;c vấn đề về tinh thần, bỏ học, phải lao động sớm hay tảo h&ocirc;n.</p>\n<p class="Normal">Khi c&aacute;c lớp học từ xa bắt đầu trở lại trong tuần n&agrave;y, Bộ trưởng Gi&aacute;o dục Philippines Leonor Briones đ&atilde; cố gắng khắc họa đ&acirc;y l&agrave; một th&agrave;nh c&ocirc;ng lớn. Theo b&agrave;, khoảng 23 triệu trẻ em, từ cấp tiểu học đến trung học, đ&atilde; đăng k&yacute; học. Nhưng b&agrave; thừa nhận rằng con số tuyển sinh năm nay &iacute;t hơn khoảng hai triệu em so với năm ngo&aacute;i.</p>\n<p class="Normal">Regina Tolentino, ph&oacute; tổng thư k&yacute; Hiệp hội Bi&ecirc;n tập vi&ecirc;n B&aacute;o ch&iacute; Đại học Philippines, cho biết việc ch&iacute;nh quyền cố gắng khắc họa một c&aacute;ch t&iacute;ch cực việc đ&oacute;ng cửa trường học năm thứ hai li&ecirc;n tiếp l&agrave; "kh&ocirc;ng thực tế".</p>\n<p class="Normal">Với học online l&agrave; lựa chọn duy nhất, gia đ&igrave;nh những học sinh ngh&egrave;o sẽ phải tốn th&ecirc;m một khoản kh&ocirc;ng nhỏ mua m&aacute;y t&iacute;nh v&agrave; thẻ Internet, c&oacute; thể lạm v&agrave;o tiền mua nhu yếu phẩm. "Ch&iacute;nh phủ phải lắng nghe học sinh v&agrave; duy tr&igrave; những quyền cơ bản li&ecirc;n quan đến gi&aacute;o dục của c&aacute;c em, ngay cả trong thời kỳ dịch bệnh", b&agrave; nhấn mạnh.</p>\n<p class="Normal">Tuy nhi&ecirc;n, c&aacute;c b&aacute;c sĩ v&agrave; chuy&ecirc;n gia y tế h&agrave;ng đầu cho rằng d&ugrave; mở cửa trường học l&agrave; một mục ti&ecirc;u quan trọng, an to&agrave;n cho sức khỏe cộng đồng vẫn cần được ưu ti&ecirc;n hơn.</p>\n<p class="Normal">Đến nay, mới chỉ c&oacute; 14 triệu người Philippines được ti&ecirc;m chủng đầy đủ, thấp hơn nhiều so với mục ti&ecirc;u ban đầu của ch&iacute;nh phủ l&agrave; ho&agrave;n th&agrave;nh ti&ecirc;m vaccine cho 70 triệu người v&agrave;o cuối năm nay. Một số bệnh viện đang rơi v&agrave;o t&igrave;nh trạng qu&aacute; tải v&agrave; cảnh tượng bệnh nh&acirc;n phải thở oxy giữa b&atilde;i đỗ xe đ&atilde; trở n&ecirc;n phổ biến.</p>\n<p class="Normal">Tiến sĩ Anthony Leachon, chuy&ecirc;n gia h&agrave;ng đầu về y tế c&ocirc;ng cộng, th&agrave;nh vi&ecirc;n ban cố vấn Covid-19 của ch&iacute;nh phủ Philippines, k&ecirc;u gọi giới chức nhanh ch&oacute;ng ti&ecirc;m chủng cho trẻ em 12-17 tuổi nhằm mở đường mở cửa trở lại trường học.</p>\n<p class="Normal">"Mở cửa trường học trong l&uacute;c biến chủng Delta đang l&acirc;y lan như hiện nay thực sự nguy hiểm", &ocirc;ng n&oacute;i.</p>\n</article>\n</div>',
      description:
        "PHILIPPINES - Học online là lựa chọn duy nhất của hai con nhà Talic, ngoại ô Manila suốt hai năm qua, giữa muôn nỗi lo về chi phí và mạng Internet.",
      createdAt: "2021-09-16T08:15:16.000Z",
      updatedAt: "2021-09-16T08:15:16.000Z",
      parent_id: 3,
      child_id: 7,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/HxwzeIuNowayAOtXDLPl.png",
      id: 13,
      title: "Taliban trải thảm đỏ cho sáng kiến Vành đai và Con đường",
      detail:
        '<div class="header-content width_common">\n<p class="description">Ch&iacute;nh quyền mới của Taliban b&agrave;y tỏ sẵn s&agrave;ng tham gia s&aacute;ng kiến V&agrave;nh đai v&agrave; Con đường của Trung Quốc, nhưng Bắc Kinh được cho l&agrave; sẽ thận trọng.</p>\n<article class="fck_detail ">\n<p class="Normal">Đầu tuần trước, ph&aacute;t ng&ocirc;n vi&ecirc;n Taliban Zabiullah Mujahid cho biết ch&iacute;nh quyền mới tại Afghanistan muốn tham gia H&agrave;nh lang Kinh tế Trung Quốc - Pakistan (CPEC), dự &aacute;n trị gi&aacute; 50 tỷ USD thuộc s&aacute;ng kiến V&agrave;nh đai v&agrave; Con đường (BRI) của Bắc Kinh.</p>\n<p class="Normal">Ngoại trưởng Pakistan Shah Mehmood Qureshi h&ocirc;m 8/9 cũng chủ tr&igrave; một cuộc họp trực tuyến với những người đồng cấp tại Trung Quốc, Iran, Tajikistan, Turkmenistan v&agrave; Uzbekistan, c&aacute;c nước l&aacute;ng giềng của Afghanistan, để thảo luận về tương lai quốc gia n&agrave;y.</p>\n<p class="Normal">"T&igrave;nh h&igrave;nh tại Afghanistan vẫn phức tạp v&agrave; dễ biến động. Ch&uacute;ng t&ocirc;i hy vọng t&igrave;nh h&igrave;nh ch&iacute;nh trị sẽ sớm ổn định v&agrave; dẫn đến trạng th&aacute;i b&igrave;nh thường. Thực tế mới đ&ograve;i hỏi ch&uacute;ng ta phải loại bỏ những g&oacute;c nh&igrave;n cũ, ph&aacute;t triển những hiểu biết mới v&agrave; tiến h&agrave;nh c&aacute;ch tiếp cận thực tế, hợp l&yacute;", Qureshi viết tr&ecirc;n Twitter sau đ&oacute;.</p>\n<p class="Normal">Tại cuộc họp, Trung Quốc cam kết viện trợ khẩn cấp 31 triệu USD cho Afghanistan, bao gồm ngũ cốc, đồ d&ugrave;ng m&ugrave;a đ&ocirc;ng, vaccine v&agrave; thuốc. "Điều Trung Quốc c&oacute; thể l&agrave;m b&acirc;y giờ l&agrave; duy tr&igrave; những mối li&ecirc;n hệ cần thiết với Taliban trong c&aacute;c hoạt động kinh tế th&ocirc;ng thường v&agrave;o trao đổi giữa người d&acirc;n", tờ Global Times thuộc People\'s Daily, cơ quan ng&ocirc;n luận của đảng Cộng sản Trung Quốc, cho hay.</p>\n<p class="Normal">Theo một nguồn tin giấu t&ecirc;n th&acirc;n cận với Taliban, Trung Quốc đ&atilde; hứa hẹn với họ về những dự &aacute;n tiềm năng tại Afghanistan kể từ năm 2018. "C&oacute; những thỏa thuận miệng về đầu tư giữa Bắc Kinh v&agrave; Taliban. Ngay khi ch&iacute;nh quyền của Taliban được to&agrave;n cầu c&ocirc;ng nhận, Trung Quốc sẽ bắt đầu tiến h&agrave;nh c&aacute;c dự &aacute;n cơ sở hạ tầng tại đất nước đ&atilde; bị chiến tranh t&agrave;n ph&aacute;", nguồn tin n&oacute;i.</p>\n<p class="Normal">Andrew Small, chuy&ecirc;n gia cấp cao về ch&acirc;u &Aacute; tại Quỹ Marshall của Đức, cũng cho rằng những đề nghị đầu tư ngay lập tức của Taliban tạo đ&ograve;n bẩy cho Bắc Kinh. Tuy nhi&ecirc;n, &ocirc;ng nhận định&nbsp;<strong>mặc d&ugrave; Trung Quốc sẽ nhanh ch&oacute;ng cung cấp một số hỗ trợ kinh tế, việc hợp t&aacute;c s&acirc;u rộng hơn c&oacute; lẽ diễn ra một c&aacute;ch thận trọng.</strong></p>\n<p class="Normal">"Bắc Kinh sẽ rất vui l&ograve;ng đưa ra những hứa hẹn, hay tham gia c&aacute;c cuộc thảo luận về triển vọng mở rộng BRI v&agrave; CPEC. Tuy nhi&ecirc;n, họ sẽ kh&ocirc;ng triển khai bất cứ việc g&igrave; tr&ecirc;n thực tế, cho đến khi đủ tin tưởng v&agrave;o c&aacute;c điều kiện ch&iacute;nh trị v&agrave; an ninh", Small ph&acirc;n t&iacute;ch.</p>\n<p class="Normal">C&aacute;c chuy&ecirc;n gia c&ograve;n chỉ ra rằng Taliban kh&ocirc;ng c&oacute; nhiều lựa chọn về nh&agrave; đầu tư. "Trung Quốc được dự đo&aacute;n sẽ sẵn s&agrave;ng gi&uacute;p ch&iacute;nh quyền của Taliban, sau khi Mỹ r&uacute;t khỏi khu vực rất quan trọng với Trung Quốc", Hasaan Khawar, nh&agrave; ph&acirc;n t&iacute;ch ch&iacute;nh s&aacute;ch c&ocirc;ng tại Pakistan, đ&aacute;nh gi&aacute;.</p>\n<p class="Normal">Trung Quốc dường như nắm r&otilde; về nguồn t&agrave;i nguy&ecirc;n kho&aacute;ng sản dồi d&agrave;o tại Afghanistan, bao gồm mỏ đồng Mes Aynak được cho l&agrave; lớn thứ hai thế giới về trữ lượng khai th&aacute;c. Tập đo&agrave;n Luyện kim Trung Quốc từng k&yacute; hợp đồng cho thu&ecirc; thăm d&ograve; mỏ n&agrave;y trị gi&aacute; 3 tỷ USD v&agrave;o năm 2008, nhưng c&ocirc;ng việc bị đ&igrave;nh trệ hơn một thập kỷ v&igrave; những lo ngại an ninh.</p>\n<p class="Normal">Theo giới quan s&aacute;t, việc dự &aacute;n thăm d&ograve; mỏ Mes Aynak bị "đắp chiếu" l&agrave; lời nhắc nhở hữu &iacute;ch với Trung Quốc rằng những khoản đầu tư lớn c&oacute; thể dễ d&agrave;ng thất bại như thế n&agrave;o trong bối cảnh bất ổn.</p>\n<p class="Normal">"Đồng rất cần thiết đối với d&acirc;y điện, đồ điện tử, động cơ v&agrave; nhiều sản phẩm kh&aacute;c được sản xuất tại Trung Quốc, nhưng họ sẽ kh&ocirc;ng vội v&agrave;ng tự đặt m&igrave;nh v&agrave;o t&igrave;nh thế rủi ro", Jeremy Garlick, gi&aacute;o sư về quan hệ quốc tế tại Đại học Kinh tế Prague của Czech, nhận định. "T&ocirc;i cho rằng Trung Quốc sẽ c&acirc;n nhắc kỹ lưỡng bất kỳ quyết định n&agrave;o về can thiệp s&acirc;u hơn v&agrave;o Afghanistan".</p>\n<p class="Normal">Nguy cơ xảy ra giao tranh, c&ugrave;ng khả năng l&atilde;nh thổ Afghanistan bị lợi dụng l&agrave;m bệ ph&oacute;ng cho chủ nghĩa khủng bố to&agrave;n cầu l&agrave; mối lo ngại lớn nhất đối với cộng đồng quốc tế, bao gồm Trung Quốc. C&aacute;c chuy&ecirc;n gia tin rằng nếu Taliban kh&ocirc;ng kiểm so&aacute;t được vấn đề n&agrave;y, Trung Quốc sẽ kh&ocirc;ng thể đầu tư d&ugrave; muốn hay kh&ocirc;ng.</p>\n<p class="Normal">Theo chuy&ecirc;n gia Small, Trung Quốc kh&ocirc;ng muốn Afghanistan dưới thời Taliban trở th&agrave;nh trung t&acirc;m kết nối tại khu vực nếu c&aacute;c phiến qu&acirc;n dễ d&agrave;ng hoạt động ở nước n&agrave;y.</p>\n<p class="Normal">"Bắc Kinh vốn lo ngại về điều kiện an ninh ng&agrave;y c&agrave;ng xấu đi đối với CPEC. Giờ đ&acirc;y, họ lại đặt ra những c&acirc;u hỏi lớn về khả năng Tehrik-i-Taliban Pakistan, nh&aacute;nh tại Pakistan của Taliban, được kh&iacute;ch lệ v&agrave; ủng hộ sau chiến thắng của Taliban", Small cho hay.</p>\n<p class="Normal">Trong khi đ&oacute;, giới chức Pakistan hoan ngh&ecirc;nh mong muốn tham gia CPEC của Taliban. Tại cuộc họp b&aacute;o h&ocirc;m 7/9, Bộ trưởng Nội vụ Pakistan Sheikh Rashid Ahmed cho biết tiến tr&igrave;nh của Pakistan v&agrave; Afghanistan c&oacute; mối li&ecirc;n hệ với nhau.</p>\n<p class="Normal">Tuy nhi&ecirc;n, một số chuy&ecirc;n gia đ&aacute;nh gi&aacute; Pakistan cần thận trọng về việc cho ph&eacute;p CPEC được mở rộng sang Afghanistan.</p>\n<p class="Normal">"Pakistan vốn gặp rắc rối về ổn định nội bộ v&agrave; chủ nghĩa khủng bố. Nếu mở rộng CPEC đến Afghanistan, rủi ro sẽ c&agrave;ng gia tăng", Garlick, t&aacute;c giả một cuốn s&aacute;ch về BRI, cảnh b&aacute;o.</p>\n</article>\n</div>',
      description:
        "Chính quyền mới của Taliban bày tỏ sẵn sàng tham gia sáng kiến Vành đai và Con đường của Trung Quốc, nhưng Bắc Kinh được cho là sẽ thận trọng.",
      createdAt: "2021-09-16T08:17:53.000Z",
      updatedAt: "2021-09-16T08:17:53.000Z",
      parent_id: 3,
      child_id: 8,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/IpVmaCrIAnBceSHYndrY.png",
      id: 14,
      title: "Những người Việt ở Mỹ ủng hộ tiêm chủng cho trẻ em",
      detail:
        '<div class="header-content width_common">\n<p class="description">Nhiều cha mẹ Việt ở Mỹ mong muốn con sớm được ti&ecirc;m vaccine giữa l&uacute;c t&igrave;nh h&igrave;nh Covid-19 diễn biến phức tạp với nhiều chủng mới như hiện nay.</p>\n<article class="fck_detail ">\n<p class="Normal">9 th&aacute;ng sau khi Mỹ bắt đầu triển khai kế hoạch ti&ecirc;m vaccine Covid-19 cho người d&acirc;n, 73% người từ 12 tuổi ở nước n&agrave;y đ&atilde; ti&ecirc;m &iacute;t nhất một mũi v&agrave; 63% ti&ecirc;m đủ mũi. Tuy nhi&ecirc;n, giới chức y tế Mỹ vẫn chưa mở rộng đối tượng ti&ecirc;m chủng cho những trẻ dưới 12 tuổi, chiếm khoảng 15% d&acirc;n số.</p>\n<p class="Normal">Điều n&agrave;y khiến nhiều cha mẹ ở Mỹ lo lắng con c&aacute;i c&oacute; thể bị nhiễm nCoV v&agrave; truyền virus cho những người xung quanh.</p>\n<p class="Normal">"B&eacute; đầu nh&agrave; t&ocirc;i 8 tuổi v&agrave; b&eacute; thứ hai mới một th&aacute;ng tuổi. Cả hai đều chưa đủ tuổi ti&ecirc;m chủng vaccine Covid-19 theo quy định ở Mỹ. Điều n&agrave;y khiến t&ocirc;i thực sự lo lắng", chị Kim Tuyền, người Việt sống ở Houston, bang Texas, chia sẻ với&nbsp;<em>VnExpress</em>.</p>\n<p class="Normal">Cơ quan Quản l&yacute; Thực phẩm v&agrave; Dược phẩm Mỹ (FDA) đ&atilde; cấp ph&eacute;p ti&ecirc;m chủng vaccine Pfizer cho trẻ em từ 12-15 tuổi v&agrave;o th&aacute;ng 5. Moderna đang chờ FDA ph&ecirc; duyệt vaccine của h&atilde;ng cho trẻ từ 12-17 tuổi, sau khi xin ph&eacute;p sử dụng khẩn cấp v&agrave;o th&aacute;ng 6. FDA c&oacute; thể sớm bật đ&egrave;n xanh cho vaccine n&agrave;y, theo&nbsp;<em>Insider</em>.</p>\n<p class="Normal">Pfizer v&agrave; Moderna đ&atilde; bắt đầu thử nghiệm l&acirc;m s&agrave;ng cho nh&oacute;m tuổi nhỏ hơn từ th&aacute;ng 3 v&agrave; kết quả nghi&ecirc;n cứu hiện chưa được c&ocirc;ng bố. Pfizer dự kiến c&oacute; dữ liệu về hiệu quả của vaccine đối với nh&oacute;m tuổi từ 5-11 v&agrave;o cuối th&aacute;ng 9. Scott Gottlieb, cựu ủy vi&ecirc;n FDA, cho rằng cơ quan n&agrave;y c&oacute; thể sẽ "bật đ&egrave;n xanh" ti&ecirc;m chủng cho trẻ dưới 12 tuổi v&agrave;o cuối m&ugrave;a thu hoặc m&ugrave;a đ&ocirc;ng.</p>\n<p class="Normal">"T&ocirc;i ủng hộ việc mở rộng ti&ecirc;m chủng cho trẻ em", chị Tuyền n&oacute;i. "Phụ nữ mang thai v&agrave; trẻ em l&agrave; những đối tượng cần được bảo vệ nhiều hơn".</p>\n<p class="Normal">Chị Tuyền cho biết đ&atilde; ti&ecirc;m một mũi Moderna v&agrave; sẽ ti&ecirc;m mũi hai sau hơn 10 ng&agrave;y nữa. Ti&ecirc;m vaccine khiến chị cảm thấy y&ecirc;n t&acirc;m hơn, trong bối cảnh dịch bệnh gia tăng ở Mỹ trong thời gian gần đ&acirc;y.</p>\n<p class="Normal">"Đợt t&ocirc;i ti&ecirc;m ch&iacute;nh phủ c&ograve;n cho mỗi người 100 USD, đồng thời tiếp tục khuyến nghị người d&acirc;n ti&ecirc;m chủng", chị kể.</p>\n<p class="Normal">Mỹ, v&ugrave;ng dịch lớn nhất thế giới, đ&atilde; b&aacute;o c&aacute;o hơn 41,5 triệu ca nhiễm v&agrave; hơn 673.000 ca tử vong kể từ khi dịch b&ugrave;ng ph&aacute;t. Với sự xuất hiện của chủng Delta, số ca nhiễm ở Mỹ tăng mạnh gần đ&acirc;y, với hơn 100.000 ca mỗi ng&agrave;y.</p>\n<p class="Normal">Tiến sĩ Anthony Fauci, gi&aacute;m đốc Viện Dị ứng v&agrave; Bệnh truyền nhiễm Quốc gia Mỹ, h&ocirc;m 9/9 cho biết tỷ lệ l&acirc;y nhiễm cao hiện nay c&oacute; thể cản trở phần n&agrave;o kế hoạch quay lại cuộc sống b&igrave;nh thường của nước Mỹ. "Với quy m&ocirc; đất nước của ch&uacute;ng ta th&igrave; kh&ocirc;ng thể loanh quanh mức 100.000 ca nhiễm mỗi ng&agrave;y. Con số phải giảm xuống dưới 10.000 trước khi người d&acirc;n bắt đầu cảm thấy thoải m&aacute;i", Fauci n&oacute;i.</p>\n<p class="Normal">Anh Phương Nguyễn, sống ở th&agrave;nh phố Sugar Land, bang Texas, cho biết anh cũng sẵn s&agrave;ng ti&ecirc;m chủng cho hai con nếu Mỹ mở rộng kế hoạch ti&ecirc;m vaccine cho nh&oacute;m dưới 12 tuổi.</p>\n<p class="Normal">"T&ocirc;i c&oacute; hai b&eacute;, ba tuổi v&agrave; một tuổi rưỡi. Nếu kế hoạch ti&ecirc;m chủng được th&ocirc;ng qua, t&ocirc;i sẽ cho hai con đi ti&ecirc;m", anh Phương n&oacute;i, cho biết kh&aacute; lo lắng cho hai con khi chưa được ti&ecirc;m vaccine, nhất l&agrave; khi chủng Delta l&acirc;y lan mạnh.</p>\n<p class="Normal">Số ca nhiễm mới trung b&igrave;nh 7 ng&agrave;y t&iacute;nh tới 9/9 ở Texas l&agrave; hơn 19.000 ca, tăng 16% trong hai tuần qua, trong khi số ca tử vong cũng tăng 36%, theo&nbsp;<em>NYTimes</em>. Texas đ&atilde; ghi nhận hơn 3,78 triệu ca nhiễm v&agrave; gần 59.500 ca tử vong kể từ khi dịch b&ugrave;ng ph&aacute;t. 70% số người từ 12 tuổi ở Texas đ&atilde; ti&ecirc;m &iacute;t nhất một liều vaccine Covid-19 v&agrave; 59% ho&agrave;n th&agrave;nh chương tr&igrave;nh ti&ecirc;m chủng.</p>\n<p class="Normal">"Số ca nhiễm v&agrave; tử vong v&igrave; Covid-19 ở Mỹ đa phần l&agrave; những người chưa ti&ecirc;m chủng", chị Tuyền n&oacute;i. "Nhiều người kh&ocirc;ng tin vaccine v&agrave; thậm ch&iacute; kh&ocirc;ng tin c&oacute; dịch bệnh".</p>\n<p class="Normal">Chị Tuyền th&ecirc;m rằng chị v&agrave; gia đ&igrave;nh cũng thường xuy&ecirc;n đeo khẩu trang, giữ khoảng c&aacute;ch an to&agrave;n với người xung quanh v&agrave; hạn chế tới nơi đ&ocirc;ng người để tr&aacute;nh nguy cơ l&acirc;y nhiễm.</p>\n<p class="Normal">Ngo&agrave;i c&aacute;c biện ph&aacute;p khuyến kh&iacute;ch v&agrave; k&ecirc;u gọi ti&ecirc;m chủng, Mỹ cũng bắt đầu c&oacute; những biện ph&aacute;p mạnh tay hơn để th&uacute;c đẩy tỷ lệ ti&ecirc;m chủng nhằm kiềm chế sự l&acirc;y lan của chủng Delta. Nh&agrave; Trắng tuy&ecirc;n bố ch&iacute;nh quyền Tổng thống&nbsp;<a href="https://vnexpress.net/chu-de/joe-biden-1615" rel="dofollow">Joe Biden</a>&nbsp;sẽ bắt buộc những c&ocirc;ng ty c&oacute; từ 100 nh&acirc;n vi&ecirc;n trở l&ecirc;n ti&ecirc;m chủng cho to&agrave;n bộ người lao động hoặc tiến h&agrave;nh x&eacute;t nghiệm nCoV h&agrave;ng tuần. Biện ph&aacute;p n&agrave;y dự kiến ảnh hưởng đến khoảng 80 triệu người.</p>\n<p class="Normal">"T&ocirc;i muốn hỏi những người Mỹ chưa ti&ecirc;m chủng l&agrave; bạn c&ograve;n chờ đợi điều g&igrave; nữa hay c&ograve;n cần xem x&eacute;t th&ecirc;m điều g&igrave;?", Biden n&oacute;i. "Ch&uacute;ng t&ocirc;i đ&atilde; thực hiện một chiến dịch ti&ecirc;m chủng miễn ph&iacute;, an to&agrave;n v&agrave; tiện lợi. Vaccine được FDA ph&ecirc; chuẩn. Hơn 200 triệu người Mỹ đ&atilde; ti&ecirc;m &iacute;t nhất một liều. Ch&uacute;ng t&ocirc;i đ&atilde; ki&ecirc;n nhẫn, nhưng sự ki&ecirc;n nhẫn c&oacute; giới hạn".</p>\n</article>\n</div>',
      description:
        "Nhiều cha mẹ Việt ở Mỹ mong muốn con sớm được tiêm vaccine giữa lúc tình hình Covid-19 diễn biến phức tạp với nhiều chủng mới như hiện nay.",
      createdAt: "2021-09-16T08:19:47.000Z",
      updatedAt: "2021-09-16T08:19:47.000Z",
      parent_id: 3,
      child_id: 9,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/BjLMvBJEuhomHyDLIivF.png",
      id: 15,
      title: "Hà Anh Tuấn: 'Sở hữu căn hộ đẹp là đầu tư cho hạnh phúc'",
      detail:
        '<div class="header-content width_common">\n<p class="description">Ca sĩ H&agrave; Anh Tuấn tiết lộ đang đầu tư bất động sản trong tập 5 của "The Master of Living Show - Powered by Masterise Homes".</p>\n<article class="fck_detail ">\n<p class="Normal">"The Master of Living Show" l&agrave; chuỗi talkshow do ca sĩ H&agrave; Anh Tuấn chủ tr&igrave; c&ugrave;ng c&aacute;c kh&aacute;ch mời l&agrave; những nghệ sĩ nổi tiếng v&agrave; chuy&ecirc;n gia am hiểu về phong c&aacute;ch sống. Mỗi tập lại b&agrave;n luận về nhiều chủ đề kh&aacute;c nhau như trang tr&iacute; nh&agrave; cửa, thiết kế nội thất, mang thi&ecirc;n nhi&ecirc;n v&agrave;o kh&ocirc;ng gian sống, đầu tư t&agrave;i ch&iacute;nh v&agrave; t&iacute;ch lũy cho tương lai...</p>\n<p class="Normal">Trong tập số 5 vừa ph&aacute;t s&oacute;ng, ca sĩ H&agrave; Anh Tuấn mang đến một trong những ca kh&uacute;c đ&atilde; l&agrave;m n&ecirc;n t&ecirc;n tuổi l&agrave;&nbsp;<em>Khi người m&igrave;nh y&ecirc;u kh&oacute;c&nbsp;</em>do Phan Mạnh Quỳnh s&aacute;ng t&aacute;c. B&ecirc;n cạnh đ&oacute;, giọng ca nổi tiếng của "Sao mai điểm hẹn 2006" c&ugrave;ng c&aacute;c kh&aacute;ch mời l&agrave; chuy&ecirc;n gia t&agrave;i ch&iacute;nh, bất động sản c&ugrave;ng b&agrave;n luận về chủ đề đầu tư th&ocirc;ng minh. B&ecirc;n cạnh &acirc;m nhạc, nam ca sĩ cho biết thời gian qua c&oacute; quan t&acirc;m đến nhiều k&ecirc;nh đầu tư kh&aacute;c nhau, trong đ&oacute; c&oacute; bất động sản.</p>\n<p class="Normal">Trước c&acirc;u hỏi "n&ecirc;n bắt đầu đầu tư từ k&ecirc;nh n&agrave;o", b&agrave; Th&ugrave;y Dương (chủ bi&ecirc;n của Tạp ch&iacute; ELLE Decoration Việt Nam) cho rằng, với c&aacute;c bạn trẻ, việc đầu tư sớm mang lại nhiều lợi thế để ph&aacute;t triển nguồn tiền v&agrave; t&iacute;ch lũy t&agrave;i sản. Nhờ đ&oacute;, giới trẻ c&oacute; thể nghỉ hưu sớm để d&agrave;nh thời gian để tận hưởng cuộc sống v&agrave; những sở th&iacute;ch c&aacute; nh&acirc;n. Theo c&aacute;c kh&aacute;ch mời, trong nhiều k&ecirc;nh đầu tư, người trẻ n&ecirc;n bắt đầu với việc đầu tư mua nh&agrave; hoặc căn hộ chung cư.</p>\n<p class="Normal">Thực tế chứng minh thị trường đầu tư bất động sản chưa giảm nhiệt ngay cả trong đại dịch. Ngược lại, khi đời sống c&agrave;ng c&oacute; nhiều biến động, một khoản tiết kiệm dự ph&ograve;ng c&agrave;ng trở n&ecirc;n quan trọng. Theo c&aacute;c chuy&ecirc;n gia, đầu tư v&agrave;o nh&agrave; ở l&agrave; một khoản đầu tư th&ocirc;ng minh v&igrave; c&oacute; thể định cư l&acirc;u d&agrave;i, lại l&agrave; t&agrave;i sản c&oacute; khả năng gia tăng gi&aacute; trị trong tương lai.</p>\n<p class="Normal">H&agrave; Anh Tuấn cũng tiết lộ đang đầu tư bất động sản v&agrave; cho rằng đ&acirc;y l&agrave; c&aacute;ch để "khi ngủ, tiền vẫn tự sinh s&ocirc;i". Ngo&agrave;i ra, anh cho rằng, sở hữu một căn hộ đẹp v&agrave; đầu tư v&agrave;o nội thất cũng ch&iacute;nh l&agrave; c&aacute;ch "đầu tư cho hạnh ph&uacute;c" của bản th&acirc;n.</p>\n<p class="Normal">Tuy nhi&ecirc;n, người đầu tư cần nắm một số lưu &yacute; trước khi bắt đầu quyết định đầu tư bất động sản. &Ocirc;ng B&ugrave;i Thanh T&ugrave;ng, Gi&aacute;m đốc v&ugrave;ng, Khối tư vấn t&agrave;i ch&iacute;nh - Dịch vụ kh&aacute;ch h&agrave;ng của Techcombank chia sẻ, với một khoản đầu tư d&agrave;i hạn n&ecirc;n chọn mua nh&agrave; hoặc căn hộ chung cư của những chủ đầu tư uy t&iacute;n v&agrave; c&oacute; đối t&aacute;c ng&acirc;n h&agrave;ng tin cậy.</p>\n<p class="Normal">C&ograve;n theo &ocirc;ng Jason Turnbull, Ph&oacute; tổng gi&aacute;m đốc ki&ecirc;m Gi&aacute;m đốc t&agrave;i ch&iacute;nh của Masterise Homes, những dự &aacute;n c&oacute; tiếng vang như Masteri Centre Point, Masteri West Heights, Masteri Waterfront hay Masteri Thảo Điền sẽ th&iacute;ch hợp những ai muốn trải nghiệm một lối sống đẳng cấp quốc tế m&agrave; vẫn đảm bảo lợi nhuận trong tương lai. "Để l&agrave;m điều đ&oacute;, ch&uacute;ng t&ocirc;i lu&ocirc;n đảm bảo sản phẩm đến tay người d&ugrave;ng phải chất lượng, c&oacute; thiết kế đẹp v&agrave; đi k&egrave;m g&oacute;i dịch vụ t&agrave;i ch&iacute;nh ph&ugrave; hợp", &ocirc;ng Jason chia sẻ.</p>\n<p class="Normal">Hiện, Masterise Homes cũng kết hợp c&ugrave;ng Techcombank mang đến giải ph&aacute;p hỗ trợ l&atilde;i suất khi kh&aacute;ch h&agrave;ng vay vốn mua nh&agrave; với nhiều đặc quyền, đi k&egrave;m một g&oacute;i bảo hiểm nh&acirc;n thọ để dự ph&ograve;ng rủi ro trong suốt qu&aacute; tr&igrave;nh vay. "Với tỷ lệ cho vay l&ecirc;n đến 80-90% đối với c&aacute;c sản phẩm của Masterise Homes, c&aacute;c bạn trẻ c&oacute; thể mua căn nh&agrave; đầu ti&ecirc;n m&agrave; kh&ocirc;ng phải chịu qu&aacute; nhiều &aacute;p lực", &ocirc;ng Thanh T&ugrave;ng cho biết.</p>\n</article>\n</div>',
      description:
        'Ca sĩ Hà Anh Tuấn tiết lộ đang đầu tư bất động sản trong tập 5 của "The Master of Living Show - Powered by Masterise Homes".',
      createdAt: "2021-09-16T08:30:35.000Z",
      updatedAt: "2021-09-16T08:31:01.000Z",
      parent_id: 4,
      child_id: 10,
    },
    {
      image:
        "https://tintucapi.phamtruong010.xyz/uploads/sMpQTxsoTuERBFunkizk.png",
      id: 16,
      title: "Sức hút của hương nước hoa thiên nhiên",
      detail:
        '<div class="header-content width_common">\n<p class="description">B&ecirc;n cạnh nước hoa tạo ra từ ph&ograve;ng th&iacute; nghiệm, m&ugrave;i hương từ nguy&ecirc;n liệu thi&ecirc;n nhi&ecirc;n vẫn c&oacute; chỗ đứng bởi gi&aacute; trị v&agrave; cảm x&uacute;c ch&uacute;ng mang lại.</p>\n<article class="fck_detail ">\n<p class="Normal">Nhiều thập kỷ qua, thế giới hương thơm lu&ocirc;n tồn tại hai trường ph&aacute;i: thi&ecirc;n nhi&ecirc;n (natural) v&agrave; nh&acirc;n tạo (synthetic). Người theo đuổi vế thứ nhất cho rằng những g&igrave; thuộc về tự nhi&ecirc;n l&agrave; tốt nhất v&agrave; chuộng d&ugrave;ng tinh dầu, absolute chiết xuất từ c&aacute;c lo&agrave;i hoa l&aacute;, cỏ c&acirc;y, thảo mộc... Tinh dầu, nước hoa l&agrave; c&aacute;c sản phẩm đặc trưng của trường ph&aacute;i n&agrave;y.</p>\n<p class="Normal">Với nhiều người, nước hoa kh&ocirc;ng đơn thuần chỉ che lấp m&ugrave;i h&ocirc;i cơ thể m&agrave; c&ograve;n l&agrave; thước đo phong c&aacute;ch c&aacute; nh&acirc;n. B&ecirc;n cạnh độ lưu hương, tỏa hương, những người s&agrave;nh nước hoa c&ograve;n quan t&acirc;m đến nguồn gốc nguy&ecirc;n liệu tạo m&ugrave;i. Sản phẩm chiết xuất từ hoa quả, thảo mộc, kho&aacute;ng chất thi&ecirc;n nhi&ecirc;n... ng&agrave;y c&agrave;ng được c&aacute;c t&iacute;n đồ coi trọng.</p>\n<p class="Normal">Theo chuy&ecirc;n gia Huỳnh Hải Yến - nh&agrave; s&aacute;ng lập Y25- Artisan de Parfum, thực tế c&aacute;c hương thơm tồn tại trong tự nhi&ecirc;n đều c&oacute; c&ocirc;ng thức h&oacute;a học giống hệt m&ugrave;i được tạo ra từ nguồn hương liệu nh&acirc;n tạo. Sự kh&aacute;c biệt cơ bản, dễ d&agrave;ng nhận thấy ở hai trường ph&aacute;i n&agrave;y l&agrave; nguồn gốc nguy&ecirc;n liệu.</p>\n<p class="Normal">"C&aacute;c chất h&oacute;a học tổng hợp n&ecirc;n m&ugrave;i hương nh&acirc;n tạo c&oacute; gi&aacute; th&agrave;nh rẻ hơn, tiết kiệm chi ph&iacute; sản xuất so với sử dụng hương liệu tự nhi&ecirc;n. Do đ&oacute;, d&ograve;ng nước hoa nh&acirc;n tạo được d&ugrave;ng phổ biến hơn tr&ecirc;n thị thường", chị Huỳnh Hải Yến n&oacute;i th&ecirc;m.</p>\n<p class="Normal">Nguồn hương liệu nh&acirc;n tạo kh&ocirc;ng đ&ograve;i hỏi phải ph&aacute;t triển v&agrave; mở rộng c&aacute;c c&aacute;nh đồng trồng hương liệu - vốn l&agrave; qu&aacute; tr&igrave;nh tốn k&eacute;m, cần tập trung nh&acirc;n c&ocirc;ng, nhiều c&ocirc;ng sức v&agrave; t&acirc;m huyết.</p>\n<p class="Normal">Điển h&igrave;nh l&agrave; qu&aacute; tr&igrave;nh tạo ra vani (vanilla) - hương liệu được chiết xuất từ lo&agrave;i lan thuộc chi vanilla, một giống lan địa phương của Mexico. Ch&uacute;ng l&agrave; loại gia vị đắt tiền thứ hai tr&ecirc;n thế giới, chỉ xếp sau saffaron từ c&acirc;y nghệ t&acirc;y.</p>\n<p class="Normal">Nếu muốn c&oacute; hương liệu vani tự nhi&ecirc;n, c&aacute;c nh&agrave; sản xuất phải trồng lan v&agrave; chiết t&aacute;ch vanillin từ quả tươi đ&atilde; kh&ocirc; h&eacute;o của ch&uacute;ng. Qu&aacute; tr&igrave;nh n&agrave;y rất tốn thời gian v&agrave; nặng chi ph&iacute;. V&igrave; vậy c&aacute;c nh&agrave; khoa học đ&atilde; t&igrave;m ra c&aacute;ch tạo ra phi&ecirc;n bản tổng hợp của vanillin trong ph&ograve;ng th&iacute; nghiệm.</p>\n<p class="Normal">Trong cuốn&nbsp;<em>B&iacute; mật những m&ugrave;i hương</em>, t&aacute;c giả Huỳnh Hải Yến cung cấp kiến thức cơ bản về điều chế nước hoa, kh&aacute;m ph&aacute; b&iacute; mật của cỏ c&acirc;y v&agrave; c&aacute;c t&aacute;c dụng bất ngờ m&agrave; ch&uacute;ng mang lại.</p>\n<p class="Normal">"T&ocirc;i theo đuổi trường ph&aacute;i tự nhi&ecirc;n, lu&ocirc;n thấy m&ugrave;i hương thi&ecirc;n nhi&ecirc;n chứa sức sống, tinh thần, linh hồn m&agrave; hương thơm nh&acirc;n tạo kh&ocirc;ng c&oacute;. Đ&oacute; cũng l&agrave; l&yacute; do trong aromatherapy, người ta kh&ocirc;ng d&ugrave;ng hương liệu tổng hợp (synthetic), ch&uacute;ng kh&ocirc;ng bao giờ c&oacute; cấu tr&uacute;c m&agrave; mẹ thi&ecirc;n nhi&ecirc;n ban tặng cho cỏ c&acirc;y, hoa l&aacute; hay những tảng r&ecirc;u nằm lặng y&ecirc;n tr&ecirc;n mặt đất... Nguy&ecirc;n liệu nh&acirc;n tạo kh&ocirc;ng c&oacute; sức mạnh kỳ diệu nằm s&acirc;u b&ecirc;n trong n&oacute;", t&aacute;c giả cho hay. C&aacute;c thương hiệu lớn tr&ecirc;n thế giới đang tập trung ph&aacute;t triển d&ograve;ng n&agrave;y.</p>\n<p class="Normal">Tuy nhi&ecirc;n, vẫn c&oacute; nhiều m&ugrave;i hương mới lạ được tạo ra từ ph&ograve;ng lab v&agrave; c&aacute;c nghi&ecirc;n cứu khoa học. Chuy&ecirc;n gia sẽ ph&acirc;n t&iacute;ch c&aacute;c cấu tử hương v&agrave; l&ecirc;n c&ocirc;ng thức mới, mang đến nhiều th&agrave;nh tựu cho lĩnh vực nước hoa.</p>\n<p class="Normal">"Điểm ưu việt của synthetic l&agrave; c&oacute; thể t&aacute;i tạo loạt m&ugrave;i hương tự nhi&ecirc;n từ c&aacute;c nguy&ecirc;n liệu qu&yacute; hiếm, phần n&agrave;o gi&uacute;p con người giảm bớt khai th&aacute;c v&agrave; t&agrave;n ph&aacute; m&ocirc;i trường. Với nguy&ecirc;n liệu thi&ecirc;n nhi&ecirc;n chứa một số đặc t&iacute;nh dễ g&acirc;y k&iacute;ch ứng hay dị ứng da, synthetic c&oacute; thể t&aacute;i tạo m&ugrave;i v&agrave; loại bỏ ho&agrave;n to&agrave;n điểm c&oacute; hại đ&oacute;", chuy&ecirc;n gia Huỳnh Hải Yến đề cập trong cuốn&nbsp;<em>B&iacute; mật những m&ugrave;i hương.</em></p>\n</article>\n</div>',
      description:
        "Bên cạnh nước hoa tạo ra từ phòng thí nghiệm, mùi hương từ nguyên liệu thiên nhiên vẫn có chỗ đứng bởi giá trị và cảm xúc chúng mang lại.",
      createdAt: "2021-09-16T08:36:15.000Z",
      updatedAt: "2021-09-16T08:36:15.000Z",
      parent_id: 4,
      child_id: 11,
    },
  ],
  total_page: 1,
  total: 11,
  current_page: 1,
};

export const detailPost = {
  image: "https://tintucapi.phamtruong010.xyz/uploads/oFZvIgyLZFudfyptxSnh.png",
  id: 9,
  title: "Đề xuất thay đổi quyền đăng ký sáng chế",
  detail:
    '<div class="header-content width_common">\n<p class="description">Dự thảo sửa đổi Luật Sở hữu tr&iacute; tuệ đề xuất tổ chức chủ tr&igrave; nghi&ecirc;n cứu c&oacute; quyền đăng k&yacute; s&aacute;ng chế v&agrave; c&oacute; quyền sở hữu s&aacute;ng chế khi được cấp văn bằng bảo hộ.</p>\n<article class="fck_detail ">\n<p class="Normal">Chiều 14/9, Thường vụ Quốc hội cho &yacute; kiến về dự &aacute;n Luật sửa đổi, bổ sung một số điều của Luật Sở hữu tr&iacute; tuệ. Đại diện cơ quan soạn thảo, Bộ trưởng Khoa học v&agrave; C&ocirc;ng nghệ Huỳnh Th&agrave;nh Đạt khẳng định, sở hữu tr&iacute; tuệ l&agrave; yếu tố quan trọng để ph&aacute;t triển kinh tế, x&atilde; hội v&igrave; t&agrave;i sản tr&iacute; tuệ c&oacute; gi&aacute; trị lớn trong nền kinh tế tri thức, l&agrave; nguồn lực cho ph&aacute;t triển sản xuất kinh doanh v&agrave; l&agrave; c&ocirc;ng cụ cạnh tranh hiệu quả của doanh nghiệp v&agrave; nền kinh tế.</p>\n<p class="Normal">Qua 16 năm &aacute;p dụng, Luật Sở hữu tr&iacute; tuệ đ&atilde; ph&aacute;t huy vai tr&ograve; trong việc điều chỉnh c&aacute;c quan hệ ph&aacute;p luật với loại t&agrave;i sản đặc biệt n&agrave;y. Tuy nhi&ecirc;n, theo &ocirc;ng Đạt, bối cảnh ph&aacute;t triển hiện nay c&oacute; nhiều thay đổi so với năm 2005. Việt Nam kh&ocirc;ng c&ograve;n thuần t&uacute;y l&agrave; nước "sử dụng t&agrave;i sản tr&iacute; tuệ" m&agrave; đang chuyển mạnh sang "tạo ra t&agrave;i sản n&agrave;y phục vụ cho m&ocirc; h&igrave;nh tăng trưởng theo chiều s&acirc;u". Do đ&oacute;, việc sửa đổi, bổ sung Luật Sở hữu tr&iacute; tuệ lần n&agrave;y l&agrave; y&ecirc;u cầu cấp thiết.</p>\n<p class="Normal">Một trong những điểm mới của dự &aacute;n luật l&agrave; đề xuất c&aacute;c tổ chức chủ tr&igrave; nghi&ecirc;n cứu c&oacute; quyền đăng k&yacute; s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; (l&agrave; kết quả của nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước) v&agrave; c&oacute; quyền sở hữu khi được cấp văn bằng bảo hộ, trừ tổ chức thuộc lĩnh vực quốc ph&ograve;ng, an ninh vẫn do c&aacute;c cơ quan nh&agrave; nước thực hiện đăng k&yacute;.</p>\n<p class="Normal">Theo người đứng đầu ng&agrave;nh Khoa học v&agrave; C&ocirc;ng nghệ, phương &aacute;n n&agrave;y được x&acirc;y dựng nhằm thể chế h&oacute;a Nghị quyết số 20 ng&agrave;y 1/11/2012 của Hội nghị Trung ương 6 Kh&oacute;a XI về ph&aacute;t triển Khoa học C&ocirc;ng nghệ phục vụ sự nghiệp c&ocirc;ng nghiệp h&oacute;a, hiện đại h&oacute;a trong điều kiện kinh tế thị trường định hướng x&atilde; hội chủ nghĩa v&agrave; hội nhập quốc tế. Nghị quyết c&oacute; chủ trương: "Giao quyền sở hữu c&aacute;c kết quả nghi&ecirc;n cứu khoa học v&agrave; ph&aacute;t triển c&ocirc;ng nghệ c&oacute; sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước cho cơ quan chủ tr&igrave; nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ".</p>\n<p class="Normal">"Quy định n&agrave;y sẽ tạo động lực khuyến kh&iacute;ch c&aacute;c tổ chức chủ tr&igrave; đăng k&yacute; x&aacute;c lập quyền sở hữu c&ocirc;ng nghiệp với phần kết quả nghi&ecirc;n cứu c&oacute; khả năng bảo hộ v&agrave; th&uacute;c đẩy thương mại h&oacute;a đối tượng quyền sở hữu c&ocirc;ng nghệ đ&oacute;", Bộ trưởng Khoa học C&ocirc;ng nghệ n&oacute;i.</p>\n<p class="Normal">Theo &ocirc;ng, phương &aacute;n n&agrave;y được sự đồng t&igrave;nh của đa số th&agrave;nh vi&ecirc;n Ch&iacute;nh phủ v&igrave; vẫn bảo đảm được sự quản l&yacute; của Nh&agrave; nước đối với kết quả nghi&ecirc;n cứu khoa học c&ocirc;ng nghệ được bảo hộ sở hữu c&ocirc;ng nghệ c&oacute; sử dụng ng&acirc;n s&aacute;ch. Ph&aacute;p luật của nhiều quốc gia tr&ecirc;n thế giới cũng c&oacute; quy định tương tự để th&uacute;c đẩy hoạt động nghi&ecirc;n cứu khoa học c&ocirc;ng nghệ, thương mại h&oacute;a kết quả nghi&ecirc;n cứu phục vụ ph&aacute;t triển kinh tế, x&atilde; hội. Tuy nhi&ecirc;n, để thực hiện phương &aacute;n, một số văn bản ph&aacute;p luật c&oacute; li&ecirc;n quan cần sửa đổi, bổ sung nhằm bảo đảm sự thống nhất trong hệ thống ph&aacute;p luật.</p>\n<p class="Normal">Do số &iacute;t th&agrave;nh vi&ecirc;n Ch&iacute;nh phủ đề nghị giữ nguy&ecirc;n quy định ph&aacute;p luật hiện h&agrave;nh, Ban soạn thảo đ&atilde; thiết kế phương &aacute;n 2, tr&igrave;nh xin &yacute; kiến Quốc hội. Theo đ&oacute;, quyền đăng k&yacute; s&aacute;ng chế vẫn thuộc về tổ chức, cơ quan nh&agrave; nước được giao quyền chủ đầu tư. Việc n&agrave;y sẽ bảo đảm được nguy&ecirc;n tắc t&agrave;i sản được tạo ra do Nh&agrave; nước đầu tư thuộc về Nh&agrave; nước.</p>\n<p class="Normal">D&ugrave; vậy, trong trường hợp tổ chức, cơ quan nh&agrave; nước được giao quyền chủ đầu tư thực hiện quyền đăng k&yacute; v&agrave; trở th&agrave;nh chủ văn bằng bảo hộ th&igrave; tổ chức, cơ quan đ&oacute; cũng kh&ocirc;ng c&oacute; khả năng thương mại h&oacute;a đối tượng được bảo hộ do cơ quan quản l&yacute; nh&agrave; nước kh&ocirc;ng c&oacute; chức năng kinh doanh. Việc giữ nguy&ecirc;n quy định hiện h&agrave;nh cũng kh&ocirc;ng thể giải quyết được c&aacute;c bất cập hiện nay do kh&ocirc;ng đủ căn cứ ph&aacute;p l&yacute; cho tổ chức chủ tr&igrave; kịp thời thực hiện đăng k&yacute; s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; l&agrave; kết quả nhiệm vụ khoa học c&ocirc;ng nghệ sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước trong một số trường hợp.</p>\n<p class="Normal">"Giữ quy định như cũ kh&ocirc;ng th&uacute;c đẩy được việc khai th&aacute;c, thương mại h&oacute;a; l&agrave;m cho gi&aacute; trị đầu tư của nh&agrave; nước trở n&ecirc;n k&eacute;m hiệu quả v&agrave; quan trọng nhất l&agrave; kh&ocirc;ng thể \'cởi tr&oacute;i\' để th&uacute;c đẩy nguồn lực đổi mới s&aacute;ng tạo của cả nền kinh tế", l&atilde;nh đạo Bộ Khoa học v&agrave; C&ocirc;ng nghệ nhấn mạnh.</p>\n<p class="Normal">Đại diện cơ quan thẩm tra, Chủ nhiệm Ủy ban Ph&aacute;p luật Ho&agrave;ng Thanh T&ugrave;ng cho biết, thường trực Ủy ban cơ bản nhất tr&iacute; với phương &aacute;n 1 của Ch&iacute;nh phủ. Tuy nhi&ecirc;n, &ocirc;ng T&ugrave;ng đề nghị Ban soạn thảo cần tiếp tục r&agrave; so&aacute;t, ho&agrave;n thiện phương &aacute;n n&agrave;y theo hướng bổ sung, l&agrave;m r&otilde;&nbsp;<em>"cơ chế ph&acirc;n chia hợp l&yacute; lợi &iacute;ch giữa Nh&agrave; nước, cơ quan chủ tr&igrave; v&agrave; t&aacute;c giả"</em>&nbsp;để thể chế h&oacute;a đầy đủ chủ trương của Đảng tại Nghị quyết số 20-NQ/TW.</p>\n<p class="Normal">Đồng thời, Ch&iacute;nh phủ cũng cần nghi&ecirc;n cứu mở rộng cơ chế giao quyền sở hữu tương tự đối với giống c&acirc;y trồng, quyền t&aacute;c giả v&agrave; quyền li&ecirc;n quan; khắc phục sự kh&ocirc;ng thống nhất giữa quy định về quyền đăng k&yacute;, sở hữu với kết quả của nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ c&oacute; sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước trong c&aacute;c luật hiện h&agrave;nh để bảo đảm t&iacute;nh thống nhất của hệ thống ph&aacute;p luật.</p>\n<p class="Normal">Chủ nhiệm Ủy ban Khoa học C&ocirc;ng nghệ M&ocirc;i trường L&ecirc; Quang Huy đồng t&igrave;nh với đề xuất của Ch&iacute;nh phủ ở phương &aacute;n 1, n&ecirc;n giao quyền cho c&aacute;c tổ chức khoa học c&ocirc;ng nghệ. Theo &ocirc;ng, như vậy sẽ khuyến kh&iacute;ch được sự s&aacute;ng tạo của c&aacute;c tổ chức nghi&ecirc;n cứu "nhưng cần t&iacute;nh to&aacute;n kỹ, đảm bảo ph&ugrave; hợp với luật Khoa học v&agrave; C&ocirc;ng nghệ, Đa dạng sinh học Việt Nam v&agrave; ph&aacute;p luật li&ecirc;n quan".</p>\n<div>\n<p class="Normal">Chủ tịch Quốc hội Vương Đ&igrave;nh Huệ nhấn mạnh, đ&acirc;y l&agrave; luật chuy&ecirc;n ng&agrave;nh, rất kh&oacute; nhưng sự chuẩn bị của ban soạn thảo v&agrave; thẩm tra sơ bộ của Ủy ban Ph&aacute;p luật "rất c&ocirc;ng phu, kỹ lưỡng". Dự &aacute;n luật đ&atilde; cơ bản thể chế h&oacute;a được chủ trương của Đảng, Nh&agrave; nước; ph&ugrave; hợp với Nghị quyết Trung ương 5, Kh&oacute;a XII về ho&agrave;n thiện thể chế kinh tế thị trường định hướng x&atilde; hội chủ nghĩa...</p>\n<p class="Normal">"T&ocirc;i đồng t&igrave;nh với phương &aacute;n 1 của Ch&iacute;nh phủ, giao quyền đăng k&yacute; s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; l&agrave; kết quả của nhiệm vụ khoa học v&agrave; c&ocirc;ng nghệ sử dụng ng&acirc;n s&aacute;ch nh&agrave; nước cho c&aacute;c tổ chức chủ tr&igrave; nghi&ecirc;n cứu; nhưng cần phải h&agrave;i h&ograve;a lợi &iacute;ch giữa đơn vị chủ quản, lợi &iacute;ch nh&agrave; nước v&agrave; lợi &iacute;ch t&aacute;c giả", &ocirc;ng Huệ n&oacute;i.</p>\n<p class="Normal">Dự &aacute;n Luật sửa đổi, bổ sung một số điều của Luật Sở hữu tr&iacute; tuệ sẽ được tr&igrave;nh xin &yacute; kiến đại biểu tại kỳ họp thứ 2, Quốc hội kh&oacute;a XV khai mạc cuối th&aacute;ng 10.</p>\n<p class="Normal">Dự thảo Luật sửa đổi, bổ sung 93 điều (trong đ&oacute; bổ sung 12 điều, sửa đổi 81 điều) v&agrave; b&atilde;i bỏ một điều, n&acirc;ng tổng số điều của Luật Sở hữu tr&iacute; tuệ sau khi sửa đổi l&ecirc;n 233 điều, thuộc 7 nh&oacute;m Ch&iacute;nh s&aacute;ch đ&atilde; được th&ocirc;ng qua. Đ&oacute; l&agrave; ch&iacute;nh s&aacute;ch bảo đảm quy định r&otilde; về t&aacute;c giả, chủ sở hữu quyền t&aacute;c giả, người biểu diễn, chủ sở hữu quyền li&ecirc;n quan trong c&aacute;c trường hợp chuyển nhượng, chuyển giao quyền t&aacute;c giả, quyền li&ecirc;n quan; khuyến kh&iacute;ch tạo ra, khai th&aacute;c v&agrave; phổ biến s&aacute;ng chế, kiểu d&aacute;ng c&ocirc;ng nghiệp, thiết kế bố tr&iacute; được tạo ra từ ng&acirc;n s&aacute;ch Nh&agrave; nước; tạo thuận lợi cho qu&aacute; tr&igrave;nh thực hiện thủ tục đăng k&yacute; quyền t&aacute;c giả, quyền li&ecirc;n quan, thủ tục x&aacute;c lập quyền sở hữu c&ocirc;ng nghiệp; bảo đảm mức độ thỏa đ&aacute;ng v&agrave; c&acirc;n bằng trong bảo hộ quyền sở hữu tr&iacute; tuệ; tăng cường hiệu quả hoạt động hỗ trợ về sở hữu tr&iacute; tuệ; n&acirc;ng cao hiệu quả của hoạt động bảo vệ quyền sở hữu tr&iacute; tuệ; bảo đảm thi h&agrave;nh đầy đủ v&agrave; nghi&ecirc;m t&uacute;c c&aacute;c cam kết quốc tế của Việt Nam về bảo hộ sở hữu tr&iacute; tuệ trong qu&aacute; tr&igrave;nh hội nhập.</p>\n</div>\n</article>\n</div>',
  description:
    "Dự thảo sửa đổi Luật Sở hữu trí tuệ đề xuất tổ chức chủ trì nghiên cứu có quyền đăng ký sáng chế và có quyền sở hữu sáng chế khi được cấp văn bằng bảo hộ.",
  createdAt: "2021-09-16T07:45:29.000Z",
  updatedAt: "2021-09-16T08:04:59.000Z",
  parent_id: 1,
  child_id: 1,
};
